import * as React from 'react';
import { useRef } from 'react';
import { useState } from 'react';

export default function SmallMenu({ onClose, refId, onMouseLeave, isHover,
    onSubmit, cancelText = "Annuler", title, description, trigger, open,
    validateText = "Valider", style,fullScreen, maxWidth, showActions = true,
    children
}) {
    const [left, setLeft] = useState()
    const [top, setTop] = useState()
    const ref = useRef()

    const [_open, setOpen] = React.useState()

    React.useEffect(() => {
        if(open){
            const element = document.getElementById(refId)// ref.current// document.getElementById(refId)
            const screenWidth = window.innerWidth
            const screenHeight = window.innerHeight
            const style = getComputedStyle(element)
            const _width = parseInt(style.width) || 0
            const _height = parseInt(style.height) || 0
            var rect = element.getBoundingClientRect();
            const _left = parseInt(rect.left)|| 0
            const _top = parseInt(rect.top)|| 0
            const _remainingRight = screenWidth - _width - _left
            console.log("width + left > screenWidth",_width, _left, screenWidth,  _width + _left > screenWidth)
            // if( _width + _left >= screenWidth) {
            //     if(_width + 30 > screenWidth) {
            //         setLeft(0)
            //         setWidth("100%")
            //     }
            //     else setLeft(screenWidth - _width - 30)
            // }
            setLeft(_left)
            console.log("_height + _top > screenHeigh",_height, _top, screenHeight,  _height + _top > screenHeight)
            // if( _height + _top > screenHeight) {
            //     if(_height + 30 > screenHeight) {
            //         setTop(0)
            //     }
            //     else setTop(screenHeight - _height - 30)
            // }
            setTop(_top + _height)
            setOpen(open)
            console.log('SmallMenu', title, open, _open)
        }
        else setOpen(open)
    }, [open])

    return (
        _open && <div ref={ref} style={{position: 'fixed', left: 0, top: 0, width: isHover ? "fit-content": "100%", height: isHover ? "fit-content" : "100%",  display: "flex",  overflow: "visible", zIndex: 10}}//, backgroundColor: 'rgba(0, 0, 0, 0.1)'}}
         onClick={onClose}>
            <div onMouseEnter={e=> {e.stopPropagation(); e.preventDefault()}} onMouseLeave={onMouseLeave} style={{position: 'fixed', left, top, width: "fit-content",  display: "flex",  overflow: "visible", zIndex: 0, backgroundColor: "red"}}>
            {children}
            </div>
            {/* <div onClick={onClose} style={{position: "absolute", left: -20, height: 20, width: 20, backgroundColor: "green", color: "white", paddingLeft: 5}}><i className='fa fa-check' /></div>
            <div onClick={onClose} style={{position: "absolute", left: -20, top: 30, height: 20, width: 20, backgroundColor: "red", color: "white", paddingLeft: 5}}><i className='fa fa-times fa-x' /></div> */}
        </div>
    );
}