import * as React from 'react';
import { useRouter, usePathname } from "next/navigation";
import Section from '../section';
import VariablesSingleton from '../Variables';
import Link from 'next/link';
import { lessonComplete } from '../utils/course';
import LoadingCircular from '../loading/LoadingCircular';
export default function NextLesson({ content, bp, ...props }) {

    const { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } = (props.md?.style || {})
    const { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } = (props.sm?.style || {})
    const [next, setNext] = React.useState(props?.custom?.lesson_next?.path)
    const [loading, setLoading] = React.useState(false)

    const router = useRouter()
    const pathname = usePathname();

    React.useEffect(() => {
        console.log('VariablesSingleton.getParsed("lesson.next")', VariablesSingleton.getParsed("lesson.next"))
        setNext(VariablesSingleton.getParsed("lesson.next")?.path)

    }, [VariablesSingleton.getParsed("lesson.next")?.path])



    return (
        !next ? <div /> : <LoadingCircular loading={loading}>
            <Link href="#" onClick={() => {
                setLoading(true)
                lessonComplete(VariablesSingleton.getParsed("course.guid"), VariablesSingleton.getParsed("lesson.guid"), VariablesSingleton.getParsed("global.domain"), VariablesSingleton.getParsed("global.jwt")).then(res => {
                    // router.push()
                    VariablesSingleton.set("lesson.guid", res?.next_slug)
                    window.location = res?.next_path
                    setLoading(false)
                })
            }}>
                <Section
                    parent={props}
                    disabled={true}
                    isNew={true}
                    bp={bp}
                    data={{
                        id: "child_" + props.id, children: props.children,
                        md: { style: { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } },
                        sm: { style: { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } },

                    }}
                    path="0" />
            </Link>
        </LoadingCircular>
    )
}