import API from "./API"
import { parseDomain } from "./stringUtils"

export function extractFields(columns) {
    const fields = []
    columns?.map(c => { if (!c.accessorKey?.includes('.')) fields.push(c.name) })
    return fields
}
export async function getMyResources_Server({
    type, domain, fields = ['id'], filters = {}, skip, limit, populate,
}, jwt
) {
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ['id', 'domain'],
                filters: {
                    domain: parseDomain(domain)
                },
                populate: {
                    [type]: {
                        fields: extractFields(fields),
                        ...(filters ? { filters } : {}),
                        ...(populate ? { populate } : {})
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return flattenJSON(cs?.configs[0]?.[type]) || []
    })
}

export function getFullResource({ domain, id, type, fields, populate, filters }, jwt) {
    console.log("getFullResource")
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ['id', 'domain'],
                filters: {
                    domain: parseDomain(domain)
                },
                populate: {
                    [type]: {
                        fields: extractFields(fields),
                        filters: { filters, id: id },
                        ...(populate ? { populate } : {})
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return cs?.configs[0]?.[type]?.[0] || []
    })
}

export function flattenJSON(json, path, deleteSource = false) {
    var result = {}
    if (json && Array.isArray(json)) {
        json = json.map((obj1, index) => {
            return flattenJSON(obj1, null, deleteSource)
        })
        if(path) result[path] = json
        else result = json
    }
    else if (json && typeof (json) === "object") {
        Object.entries(json).map(([k, v]) => {
            result = Object.assign({}, result, flattenJSON(v, (path ? path + "." : "") + k, deleteSource))
            
        })
        if(deleteSource) json = null
    }
    if (path) {
        if (json) result[path] = json;
        else delete result[path]
        // if (typeof (result[path]) === "object" && !Array.isArray(result[path])) delete result[path]
    }
    return result
}

export function plural(name) {
    if (name) {
        if (name?.endsWith('s')) return name
        return name + 's'
    }
    return null
}