import * as React from 'react';

export default function VideoBg({ url }) {
    const src = url?.replace(
        'https://youtu.be/',
        'https://www.youtube.com/embed/'
      )
  return (
    url && <div style={{ width: "100%", height: "100%", position: "absolute", top: 0, left: 0 }} >
        {src?.includes("youtube") ? <div className="video-background">
          <iframe  frameBorder="0" allowFullScreen height={"100%"} width={"100%"}
            src={src?.includes("?") ? src + "&autoplay=1&mute=1&loop=1" : src + "?autoplay=1&mute=1&loop=1"}>
          </iframe>
        </div>
          : !src?.startsWith("http") ? <video
            autoPlay
            muted
            loop
            style={{ height: "100%", width: "100%", objectFit: "cover" }} //object-fit:cover
          >
            <source src={"https://vod.api.video/vod/"+src+"/mp4/source.mp4" } type="video/mp4" />
          </video>
          :
          <iframe title={"Iframe"}  allowFullScreen height={"100%"} width={"100%"} style={{ objectFit: "cover" }} src={`https://embed.api.video/vod/${url}`} />

        }
      </div>
  )
}