import React, { useState, useEffect } from "react";
const COUNTDOWN_TARGET = new Date("2023-06-31T23:59:59");

const getTimeLeft = () => {
	const totalTimeLeft = COUNTDOWN_TARGET - new Date();
	const jours = Math.floor(totalTimeLeft / (1000 * 60 * 60 * 24));
	const heures = Math.floor((totalTimeLeft / (1000 * 60 * 60)) % 24);
	const min = Math.floor((totalTimeLeft / (1000 * 60)) % 60);
	const sec = Math.floor((totalTimeLeft / 1000) % 60);
	return { jours, heures, min, sec };
};

const Countdown = (props) => {
	const [timeLeft, setTimeLeft] = useState(() => getTimeLeft());


	useEffect(() => {
		const timer = setInterval(() => {
			setTimeLeft(getTimeLeft());
		}, 1000);

		return () => {
			clearInterval(timer);
		};
	}, []);

	const bp = props.custom?.bp
	// const transform = 'scale('+props[bp]?.w/12+')'
	const transform = {}
    const boxStyle = {
        backgroundColor : props.custom?.backgroundColor || "white",
		width: props.bp?.style?.width
    }
	const marginRight = ( 12/props[bp]?.w * 50 )+ "px"
	const marginLeft = ( 12/props[bp]?.w * 50 )+ "px"
	return (
		<div className='countdown' style={{maxWidth: "calc(100vw - 50px)"}}>
			<div className='content' style={{transform,marginRight}}>
				{Object.entries(timeLeft).map((el) => {
					const label = el[0];
					const value = el[1];
					return (
						<div className='box' key={label}>
							<div className='value' style={boxStyle}>
								<span>{value}</span>
							    <span className='label'> {label} </span>
							</div>
						</div>
					);
				})}
			</div>
		</div>
	);
};

export default Countdown;