import * as React from 'react';
import { parseContent } from '../utils/stringUtils';
import avatarIcon from "../assets/avatar.png";
import NextImage from 'next/image';
import { useLoaded } from '../utils/mediaUtils';

const getValue = (value, suffix = "px") => {
  var result = value + ""
  if (value && !result.endsWith("%") && !result.endsWith("vh") && !result.endsWith("vw")) {
    try {
      result = parseInt(result) + "px"
    } catch (e) { }
  }
  return value ? result : null
}

export default function Avatar({ content, bp, ...props }) {
  const [value, setValue] = React.useState()

  const updateValue = (_val) => {
    useLoaded({ src: _val }, res => setValue(_val && res !== 'error' ? _val : null));
  }
  React.useEffect(() => {
    parseContent(content, updateValue)
  }, [content]);

  const _style = props[bp]?.style || props.md?.style || props.style || {}
  const { height, width, marginTop, marginLeft, marginRight, marginBottom, ...style } = _style


  return (value ? <div style={{ ...style, height: getValue(height) || 40, width: getValue(width) || 40, borderRadius: "100%", overflow: "hidden" }}>
    <img src={value || avatarIcon} width="100%" title="concepteur.io" loading={"eager"} alt="Outil.IO" />
  </div> :
    <div style={{ ...style, overflow: "hidden", height: getValue(height) || 30, width: getValue(width) || 30, borderRadius: "100%", backgroundColor: "lightgray" }}>
      <NextImage src={avatarIcon} alt="Outil.IO" title='concepteur.io' />
    </div>
  )
}