const RightMenu = ({ children, bp,show, ...props }) => {
    const sideBarWidth = show ? 0 : (props[bp]?.style?.width ||props.md?.style?.width || props.style?.width || 300)
    const overflow = show ? 'visible' : 'hidden'
    return <div style={{transition: 'all 0.3s ease', marginRight: -sideBarWidth,overflow, position: "absolute", right: 0, height: "100%"}}>{children}</div>
}


export default RightMenu