import React, { useEffect, useState } from 'react'
import { memo } from "react";
import { parseContent } from '../utils/stringUtils';
import LoadingCircular from '../loading/LoadingCircular';

const TextRender =  ({content}) => {
  const [value, setValue] = useState(content)

  const updateValue = (_val) => {
    setValue(_val)
  }
    useEffect(() => {
      parseContent(content, updateValue)
    }, [content]);

    return (
      <LoadingCircular loading={!value}>
        <div className='text-render' dangerouslySetInnerHTML={{__html: value}}></div>
      </LoadingCircular>
  )
}
export default memo(TextRender);