import Cookies from 'js-cookie';
import API from './utils/API';

require('dotenv').config();
export {default as PreviewAsync} from './PreviewAsync'
export {default as Preview} from './Preview'
export {default as Active} from './Active'
export {default as API} from './utils/API'
export * as stringUtils from './utils/stringUtils'
export {default as Variables} from './Variables'


export {default as Text} from './components/base/Text';
export {default as Button} from './components/base/Button';
export {default as Col} from './components/base/Col';
export {default as Grid} from './components/base/Grid';
export {default as Row} from './components/base/Row';
export {default as Form} from './components/base/Form';
export {default as Image} from './components/base/Image';
export * as ReactDOM from 'react-dom';

export const initConcepteur = async ({domain, apiKey}) => {
    API.setBaseUrl("https://"+domain+ "/api")
    API.setConfig({domain: domain})
    // localStorage.setItem("domain", domain)
    Cookies.set("domain", domain)

    await import('./styles/index.scss')
    await import('animate.css')
}