import Base from '../Base';
import Text from './TextRender'
import Image from './Image'
import Video from './Video'
import BackgroundVideo from './BackgroundVideo'
import Avatar from './Avatar'
import Icon from './Icon'
import StarRating from './StarRating'
import Calendar from './Calendar'
import CountDown from './CountDown'
import Button from './Button'
import Section from "../section";
import TextField from "./TextField";
import Select from "./SelectPreview";
import Folder from "./Folder";
import List from "./List";
import Tableau from "./Tableau";
import TabPanel from "./TabPanel";
import PlaceHolder from "./PlaceHolder";
import MenuContainer from "./MenuContainer";
import dynamic from 'next/dynamic'
import Loading from "../loading/Loading";
import { complexTypes } from '../data/types';
import CarouselPage from './Carousel';
import Form from './Form';
// import Modules from './Modules';
import Enroll from './Enroll';
import PrevLesson from './PrevLesson';
import NextLesson from './NextLesson';
import CourseGoals from './CourseGoals';
import CourseRequirements from './CourseRequirements';
import CourseResources from './CourseResources';
import NavList from './NavList';

const Table = dynamic(() => import('./Table'), {
    loading: () => <Loading loading={true} />,
})
const Modules = dynamic(() => import('./Modules'), {
    loading: () => <Loading loading={true} />,
})
const BarLineChart = dynamic(() => import('./barLineChart'), {
    loading: () => <Loading loading={true} />,
})
const PieChart = dynamic(() => import('./pieChart'), {
    loading: () => <Loading loading={true} />,
})
const BarChart = dynamic(() => import('./barChart'), {
    loading: () => <Loading loading={true} />,
})
const ImageForm = dynamic(() => import('./Form'), {
    loading: () => <Loading loading={true} />,
})

const style = {};

const renderComponent = (element, props, bp) => {
    switch (element.type) {
        case 'barChart':
            return <BarChart {...element} bp={bp} />
        case 'barLineChart':
            return <BarLineChart {...element} bp={bp} />
        case 'pieChart':
            return <PieChart {...element} bp={bp} />
        case 'image':
            return <Image {...element} bp={bp} />
        case 'avatar':
            return <Avatar {...element} bp={bp} />
        case 'stars':
            return <StarRating {...element} bp={bp} />
        case 'video':
            return <Video {...element} bp={bp} />
        case 'backgroundVideo':
            return <BackgroundVideo {...element} bp={bp} />
        case 'icon':
            return <Icon {...element} bp={bp} />
        case 'block':
            return <div {...element} />
        case 'button':
            return <Button {...element} bp={bp} />
        case 'enroll':
            return <Enroll {...element} bp={bp} />
        case 'previous_lesson':
            return <PrevLesson {...element} bp={bp} />
        case 'next_lesson':
            return <NextLesson {...element} bp={bp} />
        case 'textfield':
            var { style: _style, ...otherProps } = element
            return <TextField {...otherProps} field={element?.custom} {...element?.custom} bp={bp} />
        case 'number':
            return <TextField {...element} field={element?.custom}  {...element?.custom} bp={bp} />
        case 'phone':
            return <TextField {...element} field={element?.custom} bp={bp} />
        case 'select':
            return <Select {...element} field={element?.custom} bp={bp} />
        case 'table':
            return <Table {...element} bp={bp} />
        case 'tableau':
            return <Tableau {...element} bp={bp} />
        case 'calendar':
            return <Calendar {...element} bp={bp} />
        case 'countdown':
            return <CountDown {...element} bp={bp} />
        case 'imageForm':
            return <ImageForm {...element} bp={bp} />
        case 'form':
            return <Form {...element} bp={bp} />
        case 'carousel':
            return <CarouselPage {...element} bp={bp} />
        case 'placeHolder':
            return <PlaceHolder {...element} bp={bp} />
        case 'list':
            return <List data={element} path="0" {...element} onClickIndex={props?.onClickIndex} bp={bp} />
        case 'nav_list':
            return <NavList data={element} path="0" {...element} onClickIndex={props?.onClickIndex} bp={bp} />
        case 'tab':
            return <TabPanel data={element} path="0" {...element} bp={bp} />
        case 'column':
            return <Section {...element} data={element} bp={bp} path="0" />
        case 'menuContainer':
            return <MenuContainer data={element} path="0" {...element} bp={bp} />
        case 'grid':
            return <Section {...element} data={element} bp={bp} path="0" />
        case 'section':
            return <Section {...element} data={element} bp={bp} path="0" />
        case 'folder':
            return <Folder {...element} bp={bp} />
        case 'wave':
            return <div dangerouslySetInnerHTML={{ __html: element.content }} />
        case 'modules':
            return <Modules {...element} bp={bp} />
        case 'objectifs':
            return <CourseGoals {...element} bp={bp} />
        case 'resources':
            return <CourseResources {...element} bp={bp} />
        case 'requirements':
            return <CourseRequirements {...element} bp={bp} />
        default:
            return (
                <Text {...element} bp={bp} />
            )
    }
}

const Component = ({ parent, previewId, data, bp, components, render, path, parentHandleDelete, handlePaste, handleDrop, childrenCount, rawChildren, ...props }) => {
    return (
        <Base {...props}
            parent={parent}
            element={data}
            style={style}
            bp={bp}
            previewId={previewId}
            className={(complexTypes.indexOf(data.type) != -1 ? (data.xtype || data.type) : "component")} {...data} item>
            {rawChildren || render ? render : renderComponent(data, props, bp)}
        </Base>
    );
}

export default Component;