// import { generateReact } from "../generator-module/generatorReact";
// import PopupSingleton from "./hooks/Popups";
import { getJsonKey } from "./utils/jsonParser";
import Variables from "./Variables";

let instance;
let activeElement = null
let configBox = null
let tabPanel = null
let allUpdates = {}
let blobs = {}
let copyElement = null
let setIsActive = null
let _setBp = null
let bp = "md"
let refresh = {}
let updateWebsiteConfig = null
const previous = []
var next = []
var deletedItems = {}
var unsavedChanges = false
var setHasRedo = () => { }
var setHasUndo = () => { }
let screenWidth
let screenHeight
let methods = {}

const purgeElement = (el) => {
  delete el.isNew
  delete el.order
  delete el.forwardClick
  delete el.triggerOnUpdate
  delete el.hideSelector
  delete el.icon
  return el
}

const deleteStyle = (el) => {
  delete el.isNew
  delete el.order
  delete el.forwardClick
  delete el.triggerOnUpdate
  delete el.hideSelector
  delete el.md?.style
  delete el.sm?.style
  delete el.style
  delete el.onClick?.style
  delete el.onHover?.style
  delete el.description
  delete el.label
  delete el.thumbnail
  delete el.logo
  delete el.icon
  delete el.name
  delete el.generatedStylemd
  delete el.generatedStylesm
  return el
}
class Active {
  constructor() {
    if (instance) {
      throw new Error("You can only create one instance!");
    }
    instance = this;
  }

  getInstance() {
    return this;
  }

  setConfigBox(cb) {
    configBox = cb
  }

  setScreenSize(width, height) {
    screenWidth = width
    screenHeight = height
  }

  getScreenWidth() {
    return screenWidth
  }
  getScreenHeight() {
    return screenHeight
  }

  setWebsiteConfig(values) {
    if (updateWebsiteConfig) updateWebsiteConfig(values)
  }

  initBp(_setBp_) {
    _setBp = _setBp_
  }
  setTabPanel(tp) {
    tabPanel = tp
  }

  setRefresh(id, func) {
    refresh[id] = { refresh: func }
  }

  triggerRefresh(id, data) {
    if (refresh[id]?.refresh) refresh[id].refresh(data)
  }

  setBp(_bp, _setBp_) {
    if (_setBp) _setBp(_bp)
    // _setBp = _setBp_
    bp = _bp

    configBox?.current?.reload(activeElement?.element)
  }
  getBp() {
    return bp
  }
  setCopyElement(el) {
    copyElement = el
  }

  getCopyElement() {
    return copyElement
  }

  getAllUpdates() {
    return allUpdates
  }
  showAddBlock() {
    tabPanel?.current?.setValue(0)
  }
  updSetIsActive(func, funcParent) {
    if (setIsActive) setIsActive(false)
    setIsActive = func
    func(true);
    // if (funcParent) {
    //   funcParent(true)
    // }
  }
  activate(el) {
    activeElement?.showContext(false)
    if (activeElement?.element?.id === el.current?.element?.id) {
      el?.current?.activate(true)
      return
    }

    if (el && el.current) {
      this.deactivate()
      el.current?.activate(true)
      activeElement = el.current
      this.loadConfig({ ...activeElement })
      tabPanel?.current?.setValue(1)
      configBox?.current?.reload(activeElement.element)
    }
  }

  deactivate() {
    activeElement?.activate(false)
    activeElement = null
  }

  showContext(el) {
    // if(activeElement?.element?.id === el.current?.element?.id) return

    this.hideContext()
    if (el && el.current) {
      this.activate(el)
      el.current?.showContext(true)
    }
  }

  hideContext() {
    activeElement?.showContext(false)
    this.deactivate()
  }

  getActive() {
    return activeElement
  }

  refresh(id) {
    if (id && allUpdates[id].refreshChild) {
      allUpdates[id].refreshChild()
    }
    if (id && allUpdates[id].refresh) {
      allUpdates[id].refresh()
    }
    else if (activeElement?.refresh) activeElement.refresh()
  }

  initHistoryButtons(_setHasUndo, _setHasRedo) {
    setHasUndo = _setHasUndo
    setHasRedo = _setHasRedo
  }

  reactivate() {
    activeElement?.activate(false)
    configBox?.current?.reload(activeElement?.element)
    activeElement?.activate(true)
  }
  reloadConfig() {
    configBox?.current?.reload(activeElement?.element)
  }
  resetConfig() {
    configBox?.current?.reload()
  }

  lastValue(id) {
    if (allUpdates[id]?.lastValue) return allUpdates[id]?.lastValue()
  }

  lastChildValue(id) {
    return allUpdates[id]?.lastChildValue()
  }

  setLastValue(id, props) {
    allUpdates[id] = {
      ...allUpdates[id],
      ...props
    }
  }

  setLastChildValue(id, props) {
    allUpdates[id] = {
      ...allUpdates[id],
      ...props
    }
  }

  loadConfig() {
    return activeElement
  }

  loadMethods(previewId, externalMethods){
    console.log("loadMethods", previewId)
    methods[previewId] = externalMethods
  }
  execMethod(previewId, name, e, {id, params, el}){
    console.log("execMethod", previewId)
    if(methods[previewId][name]) methods[previewId][name](e, {id, params, el})
  }

  updProps(key, value, scope, currentEl) {
    const _activeElement = currentEl || activeElement
    if (_activeElement) {
      previous.push({
        id: _activeElement.element?.id,
        key: key,
        previousValue: getJsonKey(_activeElement?.element, key),
        updProps: _activeElement?.updProps,
        activate: _activeElement?.activate,
        refresh: _activeElement.refresh,
        nextValue: value
      })
      next = []
      console.log("updProps in active", _activeElement.element.id, key, value, scope)
      const { children, ...newEl } = _activeElement?.updProps(key, value)
      // console.log('updProps', activeElement?.element?.id, key, value, scope)
      allUpdates[_activeElement.element.id] = { ...allUpdates[_activeElement.element.id], lastValue: _activeElement.lastValue, activate: _activeElement?.activate, updProps: _activeElement.updProps, refresh: _activeElement.refresh }
      if (scope === 'variables') {
        Variables.setScoped(_activeElement.element.id, key, value)
      }
      // console.log(previous)
      this.setUnsavedChanges(true);
      setHasUndo(true)
      if (_activeElement.element.triggerOnUpdate) _activeElement.element.triggerOnUpdate()
    }
  }
  addDeletedItem(item) {
    console.log("addDeletedItem", item.id)
    deletedItems[item?.id] = { ...item, isNew: true }
  }
  getDeletedItem(id) {
    const item = deletedItems[id]
    if (item) delete deletedItems[id]
    return item
  }
  updChildren(parentId, lastValue, updProps, activate, order, oldOrder, oldLayout) {
    if (parentId && lastValue) {
      console.log("updChildren", parentId, oldOrder, order)

      previous.push({
        id: parentId,
        key: "order",
        previousValue: oldOrder,
        updProps: updProps,
        activate: activate,
        nextValue: order
      })
      next = []
      allUpdates[parentId] = { updProps, ...allUpdates[parentId], order, lastValue }
      // console.log(previous, next)
    }
  }

  hasUnsavedChanges() {
    return unsavedChanges
  }
  setUnsavedChanges(val) {
    unsavedChanges = val
  }
  hasUndo() {
    return previous.length < 0
  }
  hasRedo() {
    return next.length > 0
  }
  undo() {
    const action = previous.pop()
    if (action) {
      console.log("undo", action.id, action.key, action.nextValue, action.previousValue,)

      if (allUpdates[action.id] && allUpdates[action.id].updProps) allUpdates[action.id].updProps(action.key, action.previousValue)
      // if(allUpdates[action.id] && allUpdates[action.id].activate) allUpdates[action.id].activate(false)
      if (allUpdates[action.id] && allUpdates[action.id].refresh) allUpdates[action.id].refresh()
      next.push(action)
      setHasRedo(true)
    }
    return previous.length > 0
  }
  redo() {
    const action = next.pop()
    if (action) {
      console.log("redo", action.id, action.key, action.previousValue, action.nextValue)

      if (allUpdates[action.id] && allUpdates[action.id].updProps) allUpdates[action.id].updProps(action.key, action.nextValue)
      // if(action.refresh) action.refresh()
      if (allUpdates[action.id] && allUpdates[action.id].refresh) allUpdates[action.id].refresh()
      previous.push(action)
      setHasUndo(true)
    }
    return next.length > 0
  }

  getDeepLastValues = (el1, clean = false, removeStyle = false) => {
    if (el1) {
      const el = this.lastValue(el1.id) || el1

      if (el.order) {
        el.children = el.order.map(c => {
          const newEl = el.children.find(x => x.id === c)
          // console.log("newEl", newEl)
          return this.getDeepLastValues(newEl || { id: c }, clean, removeStyle)
        })
      } else if (el.children) {
        el.children = el.children.map(c => this.getDeepLastValues(c, clean, removeStyle))
      }

      if (el.header) el.header = this.getDeepLastValues(el.header, clean, removeStyle)
      if (el.xheader) el.xheader = this.getDeepLastValues(el.xheader, clean, removeStyle)
      if (el.xdata) el.xdata = Array.isArray(el.xdata) ? el.xdata.map(xd => ({ ...xd, body: this.getDeepLastValues(xd.body, clean, removeStyle) })) : el.xdata
      if (el.bottomNavbar) el.bottomNavbar = this.getDeepLastValues(el.bottomNavbar, clean, removeStyle)
      if (el.body) el.body = this.getDeepLastValues(el.body, clean, removeStyle)
      if (el.navbar) el.navbar = this.getDeepLastValues(el.navbar, clean, removeStyle)
      if (el.leftsidebar) el.leftsidebar = this.getDeepLastValues(el.leftsidebar, clean, removeStyle)
      if (el.rightsidebar) el.rightsidebar = this.getDeepLastValues(el.rightsidebar, clean, removeStyle)
      if (el.template) el.template = this.getDeepLastValues(el.template, clean, removeStyle)
      if (el.footer) el.footer = this.getDeepLastValues(el.footer, clean, removeStyle)
      if (el.type === 'tableau') el.items = (this.lastChildValue(el.id, clean, removeStyle) || el.items)?.map(row => row.map(col => this.getDeepLastValues(col, clean, removeStyle)))
      if (el.type === 'form') {
        el.children = (this.getDeepLastValues({ id: "child_" + el.id }, clean)?.children || el.children)?.map(child => this.getDeepLastValues(child, clean, removeStyle))
      }

      return removeStyle ? deleteStyle({...el}) : clean ? purgeElement(el) : el
    }
  }


  setBlob(key, value) {
    blobs[key] = value
  }

  getBlobs() {
    return blobs
  }
  //   renderConfig(){
  //     return (
  //         activeElement?.data ?
  //         :<div>Cliquez sur un composant</div>
  //     )
  //   }
}

const ActiveSingleton = Object.freeze(new Active());
export default ActiveSingleton;
