import { useState, useEffect } from 'react';

import { styled } from '@mui/material/styles';
import Link from 'next/link';
import { Box } from '@mui/material'
import { useRouter } from 'next/navigation';
import VariablesSingleton from './Variables';
import API from './utils/API';
import { useSession } from 'next-auth/react';
import PaymentInfo from './PaymentInfo';



export default ({ open, redirect_url, onTrigger, callback, trigger, ...props }) => {
    const router = useRouter()
    const [_open, setOpen] = useState(open)
    const [token, setToken] = useState()
    const [error, setError] = useState()
    const [success, setSuccess] = useState()
    const [showPaymentInfo, setShowPaymentInfo] = useState(true)
    const { data: session } = useSession()
    useEffect(() => {
        // if (!session?.jwt) router.push(`https://plateforme.me/auth/signin?redirect_url=${redirect_url || VariablesSingleton.getParsed("global.url")}`)
    }, [open])

    const checkLogin = async (e) =>{
        const jwt = session?.jwt
        if (!jwt) {
            router.push(`https://plateforme.me/auth/signin?redirect_url=${VariablesSingleton.getParsed("global.url")}`)
        }
        else if(onTrigger) {
            const execOnTrigger = await onTrigger()
            setOpen(true)
            // if(!execOnTrigger) router.push(`http://localhost:3000/payment-link/20?redirect_url=${VariablesSingleton.getParsed("global.url")}`)
        }
        else setOpen(true)

    }
    const checkPaiement = (e) => {
        e.preventDefault()
        setError()
        setSuccess()
        console.log(VariablesSingleton.getParsed("courseId"))
        API.post(`/course-token/activate/${VariablesSingleton.getParsed("courseId")}`, {
            token: token,
            domain: VariablesSingleton.getParsed("global.domain")
        }, session?.jwt)
            .then(res => {
                setSuccess("Le code a bien été validé")
                setTimeout(() => {
                    window.location = `https://${VariablesSingleton.getParsed("global.domain")}/${res?.next_path}`
                }, 200);
            })
            .catch(e => {
                setError("Le code n'a pas pu être validé")
            })
    }

    return (

        <div>
            <div onClick={(e) => {  e.stopPropagation(); e.preventDefault(); checkLogin(); }}>{trigger}</div>
            {_open && <Body className="preview">
                <section className="sct col-sm-12 section_EWn row">
                    <div className="grd col-auto grid_XNq row">
                        <div className="txt col-md-12 col-sm-12 text_Lj9">
                            <p style={{ textAlign: 'center' }}><strong style={{ fontFamily: 'verdana, geneva, sans-serif', fontSize: '18px' }}>Paiement</strong></p>
                            <i className='fa fa-times'  onClick={() => setOpen(false)} style={{position: "absolute", right: 0, fontSize:25, top: 20, width: 40, height:40, color: "white"}}></i>
                        
                        </div>
                        <form className="cl col-auto column_UJ5 row" onSubmit={checkPaiement}>
                            <div className="txt col-md-12 col-sm-12" style={{ height: 'NaNpx' }}>
                                <p style={{ textAlign: 'center' }}><span style={{ fontSize: '18px', fontFamily: 'verdana, geneva, sans-serif' }}>Entrez votre code d'ACCES</span></p>
                            </div>
                            <input required className="tf col textfield_5tL" type="textfield" label="Nom" onChange={(e) => setToken(e.target.value)} />
                            {success && <p style={{ color: "green" }}>{success}<br /><br /></p>}
                            {error && <p style={{ color: "red" }}>{error}<br /><br /></p>}
                            <button className="bt button_kd6 col-auto" type="submit">
                                <p style={{ textAlign: 'center' }}><strong style={{ fontSize: '14px' }}>VALIDER</strong></p>
                            </button>
                        </form>
                        <div className="cl col-auto column_Lhb row">
                            <div className="txt col-md-12 col-sm-12 text_D7W">
                                <p style={{ textAlign: 'center' }}><span style={{ fontSize: '16px', fontFamily: 'verdana, geneva, sans-serif' }}>Ou</span></p>
                                <p style={{ textAlign: 'center' }}><span style={{ fontSize: '18px' }}><strong style={{ fontFamily: 'verdana, geneva, sans-serif' }}>Effectuer un Paiement</strong></span></p>
                            </div>
                            <img className="col-md-4 image_eTX" src="https://storage.googleapis.com/public.outil.io/medias/20/images/airtel.jpg" alt="concepteur.io" onClick={() => setShowPaymentInfo(true)}/>

                            {showPaymentInfo && <PaymentInfo type={"airtel"} onClose={() => setShowPaymentInfo(false)} />}
                            <img className="col-md-4 image_Ujp" src="https://storage.googleapis.com/public.outil.io/medias/20/images/mtn.png" alt="concepteur.io" />

                            <img className="col-md-4 image_Hrv" src="https://storage.googleapis.com/public.outil.io/medias/20/images/paypal.webp" alt="concepteur.io" />

                            <img className="col-md-4 image_V5P" src="https://storage.googleapis.com/public.outil.io/medias/20/images/visa.webp" alt="concepteur.io" />
                        </div>
                    </div>
                </section>
            </Body>}
        </div>)
}


