import * as React from 'react';
import { parseContent } from '../utils/stringUtils';

export default function Main({ content,bp, ...props }) {
  const [value, setValue] = React.useState()
  const [content1, setContent] = React.useState()
  const [value2, setValue2] = React.useState()
  
    const updateValue = (_val) => {
      if(_val)setValue(_val)
      setContent(_val)
    }
      React.useEffect(() => {
        parseContent(content, updateValue)
        parseContent(content, setContent)
        parseContent(props.custom?.urlHover, setValue2)
      }, [content]);
    return (
      <>
        <img width="100%" src={value} style={{height:props[bp]?.style?.height, maxHeight: props[bp]?.style?.maxHeight, minHeight: props[bp]?.style?.minHeight, overflow: "hidden"}} title={"concepteur.io"} onMouseOver={(e) => {if(value2) { setTimeout(() => {
           setValue(value2)
        }, 200);}}} onMouseLeave={(e) => {setTimeout(() => {
          setValue(content1)
        }, 200);}}
        loading={"lazy"} alt="concepteur.io" />
         {props.background?.color && <div style={{ width: "100%", height: "100%", position: "absolute", background: props.background?.color, opacity: props.background?.opacity / 100 || 1 }} />}
        </>
        // content?.startsWith(process.env.NEXT_PUBLIC_GCS_MEDIAS_URL) ? 
        // <Image priority={false} fill className="object-cover"
        // alt={"outil.io "} src={content} />
        // :
        // <img width="100%" src={content}
        // loading={"lazy"} alt="dashboard-builder" />
    )
}