import shortid from "../utils/shortid"
import ActiveSingleton from "../Active";
let instance;
const defaultSection = (props = {}) => ({
  id: "section_" + shortid.generate(), type: "section",
  md: { w: 12 }, sm: { w: 12 }, children: [], style: { backgroundColor: "white" }
})
let triggerElement;
let contextMenu;
const initialData = () => {
  const id = shortid.generate()
  return {
    ["popup_" + id]: {
      id: "popup_" + id,
      type: "popup",
      title: "Mon formulaire",
      children: [defaultSection({})],
      md: { style: { justifyContent: "center" } }
    }
  }
}
const initialSmallMenu = () => {
  const id = shortid.generate()
  return {
    ["popup_" + id]: {
      id: "popup_" + id,
      type: "popup",
      title: "Mon formulaire",
      children: [defaultSection({})],
      md: { w: "auto", fixedW: true }
    }
  }
}

let popups = {
  ...initialData()
}
let pages = {
}
let placeholders = {
}
let forms = {}
let popupBuilder = null
class Popups {
  constructor() {
    if (instance) {
      throw new Error("You can only create one instance!");
    }
    instance = this;
  }

  getInstance() {
    return this;
  }

  getAll() {
    return popups;
  }

  reset() {
    triggerElement = null
  }
  showPopupPreview(id) {
    var _popup = popups[id]
    popupBuilder?.current.setData(_popup || Object.values(initialData())[0])
    popupBuilder?.current.setOpen(true)
  }
  closePopup() {
    popupBuilder?.current.setOpen(false)
  }
  deletePopup(id, type) {
    if (type) { }
    else delete popups[id]
  }
  showPopup(id, trigger, propsKey) {
    // const trigger = ActiveSingleton.getActive()
    var _popup = popups[id]
    console.log("showPopup", popups, popups[id], id, propsKey, trigger)
    popupBuilder?.current.setData(_popup || Object.values(initialData())[0])
    popupBuilder?.current.setTriggerElement(trigger)
    popupBuilder?.current.setTriggerPropsKey(propsKey)
    popupBuilder?.current.setOpen(true)
    // Active.deactivate()
    popupBuilder?.current.showPopup()
    triggerElement = trigger
    if (document.getElementById(id)) {
      document.getElementById(id).style.display = "block"
      document.getElementById(id).style.position = "relative"
    }
  }
  showPopupElement(data, _trigger, propsKey, previewData) {
    const trigger = ActiveSingleton.getActive()
    console.log("showPopupElement", data, previewData)
    popupBuilder?.current.setData({ ...data?.clone, previewData } || Object.values(initialData())[0])
    popupBuilder?.current.setTriggerElement(trigger)
    popupBuilder?.current.setTriggerPropsKey(propsKey)
    popupBuilder?.current.setElementMapping(data?.mapping)
    popupBuilder?.current.setOpen(true)
    // Active.deactivate()
    popupBuilder?.current.showPopup()
    triggerElement = trigger
    if (document.getElementById(data?.id)) {
      document.getElementById(data?.id).style.display = "block"
      document.getElementById(data?.id).style.position = "relative"
    }
  }
  showPlaceHolder(id, element) {
    var _popup = popups[element]
    var _placeholder = placeholders[id]
    console.log("showPopup", id, _popup, _placeholder)
    if (_placeholder?.refresh) _placeholder?.refresh(_popup)
  }
  hideContextMenu() {
    if (contextMenu?.hide) contextMenu?.hide()
  }
  showContextMenu(path, element, coords, methods) {
    if (contextMenu?.refresh) contextMenu?.refresh(path, element, coords, methods)
  }
  initContextMenu(refresh, hide, show) {
    contextMenu = { refresh, hide, show }
  }
  showLeftMenu(show) {
    if (placeholders["leftMenu"].show != undefined) {
      console.log("showLeftMenu", show, placeholders["leftMenu"].show)
      placeholders["leftMenu"].show = !placeholders["leftMenu"].show
    }
    else {
      console.log("showLeftMenu undefined", show, placeholders["leftMenu"].show)
      placeholders["leftMenu"].show = show
    }
    return placeholders["leftMenu"].show
  }

  showRightMenu(show = true) {
    if (!show) {
      placeholders["rightMenu"].show = true
    } else {
      placeholders["rightMenu"].show = !placeholders["rightMenu"].show
    }
    return placeholders["rightMenu"].show
  }
  showPanier(show = true) {
    if (!show) {
      placeholders["panier"].show = true
    } else {
      placeholders["panier"].show = !placeholders["panier"].show
    }
    return placeholders["panier"].show
  }

  hideMenu(id) {
    var _placeholder = placeholders[id]
    if (_placeholder?.hide) _placeholder?.hide()
  }
  toogleLeftMenu() {
    var _placeholder = placeholders["leftMenu"]
    _placeholder?.toogleLeftMenu()
  }
  toogleRightMenu() {
    var _placeholder = placeholders["rightMenu"]
    _placeholder?.toogleRightMenu()
  }

  tooglePanier() {
    var _placeholder = placeholders["panier"]
    _placeholder?.tooglePanier()
  }

  getTitles() {
    return Object.values(popups).map(p => ({ value: p?.id, label: p?.title || p?.name || p?.id, lastValue: p?.lastValue }))
  }

  getPages() {
    return Object.values(pages).map(p => ({ value: p.id, label: p.title || p.name || p.id, lastValue: p.lastValue }))
  }

  getPlaceholders() {
    return Object.values(placeholders).map(p => ({ value: p.id, label: p.title || p.name || p.id, lastValue: p.lastValue }))
  }

  initBuilder(component) {
    popupBuilder = component
  }

  loadPopups(_popups) {
    popups = _popups
  }

  setForm(data) {
    const guid = forms[data?.id]?.guid || "form_" + Date.now().toString(36) + Math.random().toString(36).substring(2);
    forms[data?.id] = {
      ...data?.custom,
      name: data?.name,
      ...(data?.guid ? {} : {isNew:true}),
      type: data?.custom?.submitType,
      guid: guid,
    }
    console.log("setForm", forms[data?.id])
  }
  getForm(id){
    return forms[id] || {id, guid: "form_" + Date.now().toString(36) + Math.random().toString(36).substring(2)}
  }
  getAllForms(){
    return forms
  }

  set(id, value, type) {
    if (type && value) value.dType = type
    popups[id] = value
    console.log(popups[id])
  }
  createIfNotExists(id, value, type) {
    if (!popups[id]) {
      if (type && value) value.dType = type
      popups[id] = value
    }
  }
  setPlaceholder(id, value) {
    placeholders[id] = value
  }
  showPage(id) {
    if (id) {
      var _page = pages[id]
      var _placeholder = placeholders["page"]
      console.log("showPage", id, _page)
      _placeholder.handlePageChange(_page)
    }
  }
  setPage(id, value) {
    console.log("setPage", id, value)
    if (!pages[id]?.lastValue) pages[id] = value
  }
  get(id) {
    return popups[id]
  }
  getOrCreate(id) {
    const result = popups[id] || defaultSection()
    popups[result.id] = result
    return result
  }

}

const PopupSingleton = Object.freeze(new Popups());
export default PopupSingleton;
