import Cookies from "js-cookie"
import API, { purgeData2 } from "./API"
import nookies from 'nookies'

export async function getAllPaymentLinks_Server(
    domain,
    { skip = 0,
        limit }, jwt
) {
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ["id", "logo", "name", "domain"],
                filters: {
                    domain: domain?.startsWith("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
                },
                populate: {
                    payment_links: {
                        fields: ["id", "thumbnail", "resource_guid", "resource_type", "price", "devise"],
                        // populate:{
                        //     "payment_link": { 
                        //       fields: ["*"]
                        //     }
                        // }
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return { payment_links: cs?.configs[0]?.payment_links || [] }
    })
}

