export async function getPayment(
    id, domain, jwt
) {
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ["id", "logo", "name", "domain"],
                filters: {
                    domain: domain?.includes("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
                },
                populate: {
                    payment_links: {
                        fields: ["id", "thumbnail", "price", "devise", "resource_type", "resource_guid", "guid", "stripe_url", "redirect_url"],
                        filters: {
                            id: id
                        },
                        populate: {
                            template: {
                                fields: ["id", "thumbnail"]
                            }
                        }
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return { payment_link: cs?.configs[0]?.payment_links[0] || null }
    })
}


export async function getCoupon(
    id, domain, jwt
) {
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ["id", "logo", "name", "domain"],
                filters: {
                    domain: domain?.includes("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
                },
                populate: {
                    coupons: {
                        fields: ["id", "resource_type", "resource_guid", "status", "multiple_use", "guid", "code", "redirect_url"],
                        filters: {
                            id: id
                        }
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return { coupon: cs?.configs[0]?.coupons[0] || null }
    })
}
export async function getPaymentFull(
    id, domain, jwt
) {
    return API.get_Server(`/payment-links`, {
        fields: ["*"],
        filters: {
            id: id,
            config: { domain: domain?.includes("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain }
        },
        populate: {
            template: {
                fields: ["*"],
                populate: {
                    header: { fields: ["*"] },
                    leftsidebar: { fields: ["*"] },
                    rightsidebar: { fields: ["*"] },
                    footer: { fields: ["*"] }
                }
            }
        }
    }, jwt).then(async cs => {
        return (await purgeData2(cs?.data[0])) || null
    })
}

export async function updateBasket({ domain, jwt, resource_guid, resource_type, quantity, }) {
    return API.post(`/payment-links/update_basket`, {
        domain: domain, resource_guid, resource_type
    }, jwt).then(cs => {
        Cookies.set('basket', JSON.stringify(cs?.resources))
        Cookies.set('plink', cs?.guid)
        return cs || null
    })
}
