import * as React from 'react';
import { parseContent } from '../utils/stringUtils';

export default function Main({ content, ...props }) {
    const [value, setValue] = React.useState()
  
    const updateValue = (_val) => {
      setValue(_val)
    }
      React.useEffect(() => {
        parseContent(content, updateValue)
      }, [content]);

    return (
        <i className={value + " b-icon " + (props?.custom?.size)}/>
    )
}