import React, { useEffect, useState } from "react";
import { styled } from '@mui/material/styles';
import Section from "./section";
import PopupSingleton from "./hooks/Popups";
import { computedStyle, getPixelValue } from "./utils/baseUtils";
import { JSToCSS } from "./utils/jsonParser";
import SmallMenu from "./hooks/SmallMenu";
import VideoBg from "./VideoBg";
import OnScreenSingleton from "./hooks/OnScreen";
import { useRouter } from "next/navigation";
import ScreenSizeWatcher from "./hooks/ScreenSizeWatcher";
import ActiveSingleton from "./Active";
import { Box } from "@mui/material";

const Div = styled(Box)(({ cssstyle, backgroundhover, customcss, activestyle, hovercss, hoverstyle, type }) => (`
position: relative;
${cssstyle}
&.activeItem{${activestyle};}

${customcss};
&:hover{
    &>.scrollButton {
        display: block;
    }
    ${hoverstyle};
    ${hovercss};
}
`))

const DivRow = styled(Box)(({ cssstyle, backgroundhover, customcss, activestyle, hovercss, hoverstyle, type }) => (`
${customcss};
&.activeItem{${activestyle};}
${backgroundhover ? `&:hover{transition: 0.1s;${backgroundhover}};` : ''}
`));

const Base = ({ children, bp, element, rawChildren, previewId, updProps, type, className = "", onItemClick, parent, ...props }) => {
    const [open, setOpen] = React.useState(false)
    const [popup, setPopup] = React.useState(false)
    const [isActive, setIsActive] = React.useState(false)
    const [smallMenu, setSmallMenu] = React.useState()
    const [isHover, setIsHover] = React.useState(false)
    const [showSmallPopup, setShowSmallPopup] = React.useState(false)

    const updActive = (val) => {
        // console.log("checking is active", val, element.id, props.setParentIsActive)
        setIsActive(val)
        // if(props.setParentIsActive) props.setParentIsActive(true)
    }

    const handleHover = async (e) => {
        // setIsActive(true)
        if (props.handleHover) props.handleHover(e)
        if (element.onHover?.actionType === "close") {
            PopupSingleton.closePopup()
        }
        else if (element.onHover?.actionType === "reload") {
            location.reload()
        }
        else if (element.onHover?.actionType === "popup") {
            PopupSingleton.showPopupPreview(element.custom.popup)
        }
        else if (element.onHover?.actionType === "placeHolder") {
            // console.log("showComponent", element.custom.popup)
            PopupSingleton.showPlaceHolder(element?.custom?.placeHolder, element?.custom?.popup)
            // setOpen(true)
        } else if (element.onHover?.actionType === "smallMenu") {
            setTimeout(() => {
                toogleSmallMenu(true, true)
            }, 200);
        } else if (element.onClick?.actionType === "leftMenu") {
            PopupSingleton.toogleLeftMenu()
        } else if (element.onClick?.actionType === "rightMenu") {
            PopupSingleton.toogleRightMenu()
        } else if (element.onClick?.actionType === "panier") {
            console.log("tooglePanier",)
            PopupSingleton.tooglePanier()
        }
    }
    const handleHoverLeave = async (e) => {
        // if (element.onHover?.actionType) {
        //     PopupSingleton.closePopup()
        // } 
        if (element.onHover?.actionType === "smallMenu") {
            setTimeout(() => {
                toogleSmallMenu(false)
            }, 200);
        } else if (element.onClick?.actionType === "leftMenu") {
            PopupSingleton.toogleLeftMenu()
        } else if (element.onClick?.actionType === "rightMenu") {
            PopupSingleton.toogleRightMenu()
        } else if (element.onClick?.actionType === "panier") {
            console.log("tooglePanier",)
            PopupSingleton.tooglePanier()
        }
    }
    const handleOpen = async (e) => {
        // setIsActive(true)
        if (props.handleOnClick) props.handleOnClick(e)
        if (onItemClick) onItemClick(e)
        else if (element.onClick?.actionType === "close") {
            PopupSingleton.closePopup()
        }
        else if (element.onClick?.actionType === "reload") {
            location.reload()
        }
        else if (element.onClick?.actionType === "popup") {
            PopupSingleton.showPopupPreview(element.custom.popup)
            // setOpen(true)
        }
        else if (element.onClick?.actionType === "placeHolder") {
            // console.log("showComponent", element.custom.popup)
            PopupSingleton.showPlaceHolder(element?.custom?.placeHolder, element?.custom?.popup)
            // setOpen(true)
        } else if (element.onClick?.actionType === "url") {
            window.open(element.custom?.url, "_self")
        } else if (element.onClick?.actionType === "smallMenu") {
            toogleSmallMenu()
            setIsHover(false)
        } else if (element.onClick?.actionType === "leftMenu") {
            PopupSingleton.toogleLeftMenu()
        } else if (element.onClick?.actionType === "rightMenu") {
            PopupSingleton.toogleRightMenu()
        } else if (element.onClick?.actionType === "panier") {
            console.log("tooglePanier",)
            PopupSingleton.tooglePanier()
        } else if (element.onClick?.actionType === "page") {
            PopupSingleton.showPage(element?.custom?.page)
        } else if (element.onClick?.actionType === "login") {
            const { signIn, getProviders } = (await import('next-auth/react'))
            const providers = await getProviders()
            signIn(element?.custom?.provider || 'google')
        } else if (element.onClick?.actionType === "method") {
            ActiveSingleton.execMethod(previewId, element.onClick?.method, e, {id: element.id, params: element.custom})
        }
        else if (element.onClick?.actionType === "airtel") {
            e.preventDefault()

        }
        ActiveSingleton.updSetIsActive(updActive)
        // if(!props?.forwardClick) e.stopPropagation()
        // if (element?.onClick?.style) {
        //     e.stopPropagation()
        // }
    }
    const handleClose = (e) => {
        e.preventDefault()
        e.stopPropagation()
        setOpen(false)
    }

    const toogleSmallMenu = (show, _isHover) => {
        // console.log(parseInt(getComputedStyle(document.getElementById(element.id)).width))
        setIsHover(_isHover != undefined)
        if (show != undefined) {
            if (show) setSmallMenu(PopupSingleton.get(element?.custom?.popup))
            setShowSmallPopup(show);
            return
        }
        if (!showSmallPopup) {
            setSmallMenu(PopupSingleton.get(element?.custom?.popup))
        }
        setShowSmallPopup(!showSmallPopup)
    }

    const renderChildren = () => {
        return React.Children.map(children, (child) => child && React.cloneElement(child, {
            setParentIsActive: updActive,
        }));
    };

    useEffect(() => {
        if (element.onScreen && element.onScreen["data-osn"]) {
            const rootEl = document.getElementById(element.id)
            element.onScreen["data-osn-duration"] && rootEl.style.setProperty('--animate-duration', element.onScreen["data-osn-duration"] + 's');
            element.onScreen["data-osn-delay"] && rootEl.style.setProperty('-webkit-animation-delay', element.onScreen["data-osn-delay"] + 's');
            element.onScreen["data-osn-delay"] && rootEl.style.setProperty('animation-delay', element.onScreen["data-osn-delay"] + 's');
            element.onScreen["data-osn-repeat"] && rootEl.style.setProperty('--animate-repeat', element.onScreen["data-osn-repeat"]);
            OnScreenSingleton.observe(rootEl)
            rootEl.addEventListener('animationend', () => {
                // console.log("animationend", rootEl.className)
                rootEl.classList.remove(`animate__animated`, element.onScreen["data-osn"])
            });
        }
    }, [element.onScreen])

    const w = element[bp]?.fixedW || (element.md?.fixedW && element.md?.style?.width) ? "auto" : element[bp]?.w || (element.md?.w >= 6 ? element.md?.w : null)

    const { display: _display, direction, alignItems, justifyContent, alignContent, flexDirection, flexWrap, zIndex,
        borderRadius, borderStyle, borderWidth, borderColor,
        marginRight, marginLeft, marginTop, marginBottom, paddingRight, paddingLeft, paddingTop, paddingBottom,
        height, alignSelf, maxWidth, maxHeight, position, top, bottom, left, right, overflowX, overflowY, ..._otherStyles } = computedStyle(element.style || {}, element, bp)

    const [display, setDisplay] = useState(_display)
    function checkDisplay(screenWidth) {
        if (parseInt(element.display?.minScreenWidth) > screenWidth) { setDisplay("none"); }
        else { setDisplay(_display); }
    }

    useEffect(() => {
        if (element.display?.[bp]?.minScreenWidth) ScreenSizeWatcher.addWatcher(element.id, checkDisplay, element.display?.[bp]?.minScreenWidth)
    }, [])

    var onHover = element.onHover?.move ? element.onHover?.move + "-" + (element.onHover?.moveSpeed || "normal") : ''
    var onHoverDisplay = " " + element.onHover?.display ? element.onHover?.display : ""
    var backgroundhover = element.custom?.backgroundImageHover ? `background-image: url(${element.custom?.backgroundImageHover})!important; background-position: ${element.custom?.backgroundPositionHover}!important; background-size: ${element.custom?.backgroundSizeHover}!important` : null

    const onHoverStyle = element.onHover?.style ? JSToCSS(element.onHover?.style, "!important") : null

    var childMaxWidth = getPixelValue(element[bp]?.child, "maxWidth")
    // console.log("position display", element.id, bp, position, display, childMaxWidth)
   
    const col = w ? " col-xs-" + w + " col-sm-" + w + " col-md-" + w : " col"

    return (<>
        <Div id={element.id} className={(onHoverDisplay || "") + col + " main_preview " + " main_" + element.type + " "}
            cssstyle={element.cssStyle}
            hovercss={element.onHover?.css}
            hoverstyle={onHoverStyle}
            style={{
                height: height, maxHeight, ..._otherStyles,
                marginRight, marginLeft, marginTop, marginBottom, maxWidth, maxHeight, alignSelf, position: position || "relative",
                top, bottom, left, right, display, zIndex,
                borderRadius,
                overflowX,
                overflowY,
                ...(childMaxWidth ? { display: "flex", justifyContent: "center", overflow: 'visible' } : {}),
                ...(props.isActive || isActive ? props.onClick?.style : {})
            }}
            {...element.onScreen}
            type={element.type}
            customcss={element.customCss}
            onClick={handleOpen}
            onMouseEnter={handleHover} onMouseLeave={handleHoverLeave}>
                
            {element.background?.videoUrl && <VideoBg url={element.background?.videoUrl} />}
            {element.background?.color && <div style={{ width: "100%", height: "100%", position: "absolute", background: element.background?.color, opacity: element.background?.opacity / 100 || 1 }} />}

            {/* { element.type === "list" && overflowX === "auto" && <LeftCarouselButton className="scrollButton" onClick={() => { scrollLeft() }}>
                <i className='fa fa-chevron-left' style={{ width: 20, height: 15, borderRadius: "100%" }}></i>
            </LeftCarouselButton>} */}
            <DivRow style={{
                // minHeight: 
                height: "fit-content", width: childMaxWidth ? "100%" : "",
                paddingRight, paddingLeft, paddingTop, paddingBottom, display, position: "relative",
                borderRadius, borderStyle, borderWidth, borderColor,
                maxWidth: childMaxWidth || "100%", flexDirection, flexWrap, alignContent: alignContent || alignItems, alignItems, justifyContent,
                ...(overflowX === "visible" || childMaxWidth ? { overflow: "visible" } : {})
            }} id={"content_" + element.id}
                className={props.activeItem + " " + element.id + " " + className + ' ' + element.type + " row preview " + (element.onClick?.actionType ? " clickable " : "") + " " + onHover}
                activestyle={JSToCSS(props.onClick?.style)} backgroundhover={backgroundhover}>
                {rawChildren || renderChildren()}
            </DivRow>
            {showSmallPopup && smallMenu && <SmallMenu isHover={isHover} bp={bp} onMouseLeave={() => isHover && toogleSmallMenu(false)} onClose={() => toogleSmallMenu(false)} refId={element.id} open={smallMenu}>
                <Section data={smallMenu} bp={bp} path="0" />
            </SmallMenu>}
        </Div>
        {popup?.children && open && <div className="popup-builder popup-modal" onClick={handleClose} style={{ ...popup.style, zIndex: auto, position: "fixed", display: open ? "flex" : "none" }}>
            <div className='popup-section modal-content'>
                {
                    popup?.children?.map((c, index) => <div key={index}><Section data={c} bp={bp} /></div>)
                }
            </div>
        </div>}
    </>
    );
}

export default Base;
