import * as React from 'react';
import Component from '../component';
import Section from '../section';

export default function SimpleTab({ id, ...props }) {
  const [activeIndex, setActiveIndex] = React.useState(0)



  const verticalStyle = { display: "flex", flexDirection: "row", width: "100%", height: "100%" }
  const horizontalStyle = { display: "flex", flexDirection: "column", flexWrap: "nowrap", width: "100%", height: "100%" }
  return <>

    <Section data={props.header}
      rawChildren={(
        <div className='flex-nowrap' style={props.custom?.orientation === "row" ?  horizontalStyle : verticalStyle}>{
        props.xdata.map((xd, index) =>
          <Component key={index} onClick={(e) => { setActiveIndex(index) }} isActive={activeIndex === index} disabled={false} data={props.xheader}
            rawChildren={<>
              <span style={{ fontSize: '14px', width: "fit-content", wordBreak: "break-word", maxWidth: "calc(100% - 40px)" }}>{xd.label}</span>
              <i className={"ic fa-lg " + xd.icon} style={{ height: '40px', width: '40px', zIndex: 200 }}></i>
            </>
            } 
            />

        )
      } </div>
      )}
    />


    {/* <Component data={props.header} path="0" onClickIndex={handleChangeIndex} /> */}
    <div style={{ minHeight: 20, width: "100%" }}>
      {props.xdata.map((c, index) => activeIndex === index && <Section parent={props} data={c.body} key={index} />)}
    </div>
  </>
}