
import qs from 'qs';
import { getSession } from 'next-auth/react';
import { setCookie } from 'nookies'
import { getApiUrl } from './stringUtils';
let token = {}
let baseUrl = process.env.NEXT_PUBLIC_BASE_URL
let serverBaseUrl = process.env.NEXT_PUBLIC_BASE_SERVER_URL
let instance;
let configs;
let config;
let jwt
class APISingleton {
    constructor() {
        if (instance) {
            throw new Error("You can only create one instance!");
        }
        // const { data: session } = getSession().then(session => {

        //     if (session?.domain) {
        //         this.domain = parseDomain(session?.domain)
        //         this.baseUrl = getApiUrl(domain)
        //         this.jwt = session?.jwt
        //     }
        // })
        instance = this;
    }

    async getInstance() {
        console.log('get instance', baseUrl)
        const { data: session } = await getSession()
        if (session?.domain) {
            domain = parseDomain(session?.domain)
            baseUrl = getApiUrl(domain)
            jwt = session?.jwt
        }
        return this;
    }

    setBaseUrl(url) {
        baseUrl = url
    }
    setConfigs(_configs) {
        configs = _configs
    }
    setConfig(_config) {
        config = _config
    }
    getConfig() {
        return config
    }
    getConfigs() {
        return configs
    }
    setJwt(_jwt) {
        jwt = _jwt
    }
    getJwt() {
        return jwt
    }
    async setToken(val) {
        setCookie(null, "jwt", val)
        token = val ? { "Authorization": "Bearer " + val } : {}
    }
    getToken() {
        return token?.Authorization
    }
    setBaseUrl(value) {
        baseUrl = value
    }

    async get_Server(url, params, jwt) {
        return this._get(`${serverBaseUrl}${url}`, params, jwt)
    }
    async get(url, params, jwt) {
        return this._get(`${baseUrl}${url}`, params, jwt)
    }
    async _get(url, params, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        var response = null
        try {
            response = await fetch(
                `${url}?${qs.stringify(params, { encode: false })}`
                , {
                    headers: {
                        'content-type': 'application/json',
                        ...token
                    }
                }).then(result => result.json())
        } catch (e) {
            console.error(e)
        }
        return response
    }

    async rawSQL_Server(url, params, jwt) {
        return this._rawSQL(`${serverBaseUrl}${url}`, params, jwt)
    }
    async rawSQL(url, params, jwt) {
        return this._rawSQL(`${baseUrl}${url}`, params, jwt)
    }
    async _rawSQL(url, params, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        var response = null
        try {
            response = await fetch(`${url}`, {
                body: JSON.stringify(params),
                method: 'POST',
                headers: {
                    'content-type': 'application/json',
                    ...token
                },
            }).then(result => result.json())
        } catch (e) { console.error(e) }
        if (response) {
            if (response.type === 'scalar') {
                return {
                    data: await purgeData(response?.data)
                }
            }
            return {
                data: await purgeData(response?.data),
                cols: await purgeData(response?.cols),
                type: await purgeData(response?.type),
                count: await purgeData(response?.count),
            }

        }
    }

    async find_Server(url, params, jwt) {
        return this._find(`${serverBaseUrl}${url}`, params, jwt)
    }
    async find(url, params, jwt) {
        return this._find(`${baseUrl}${url}`, params, jwt)
    }
    async _find(url, params, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        var response = null
        try {
            response = await fetch(
                `${url}?${qs.stringify(params, { encode: false })}`,
                {
                    headers: { 'Content-Type': 'application/json', },
                    ...token
                }).then(result => result.json())
        } catch (e) {
            // console.error(e)
        }
        return {
            data: await purgeData(response?.data),
            meta: response?.meta || {},
            pagination: response?.meta?.pagination || {}
        }
    }

    async findOne_Server(url, params, jwt) {
        'use server'
        return this._findOne(`${serverBaseUrl}${url}`, params, jwt)
    }
    async findOne(url, params, jwt) {
        return this._findOne(`${baseUrl}${url}`, params, jwt)
    }
    async _findOne(url, params, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        var response = null
        try {
            response = await fetch(`${url}?${qs.stringify(params, { encode: false })}`,
                {
                    headers: { 'Content-Type': 'application/json', ...token },

                }).then(res => res.json())
        } catch (e) {
            // console.error(e)
        }
        var data = response?.data || null
        if (Array.isArray(data)) {
            data = data[0] || null
        }
        return await purgeData(data)
    }

    async findOrCreate_Server(url, data, jwt) {
        'use server'
        return this._findOrCreate(`${serverBaseUrl}${url}`, data, jwt)
    }
    async findOrCreate(url, data, jwt) {
        return this._findOrCreate(`${baseUrl}${url}`, data, jwt)
    }
    async _findOrCreate(url, data) {
        return fetch(`${url}`, {
            body: JSON.stringify({ data: data }),
            method: 'POST',
            credentials: 'include',
            mode: 'cors',
            headers: {
                'content-type': 'application/json',
                ...token
            }
        }).then(res => res.json())
            .then(async res => {
                if (!res.error) {
                    return await purgeData(res)
                }
                else throw res.error.message
            })
    }

    async createOrUpdate_Server(url, data, jwt, params) {
        'use server'
        return this._createOrUpdate(`${serverBaseUrl}${url}`, data, jwt, params)
    }
    async createOrUpdate(url, data, jwt, params) {
        return this._createOrUpdate(`${baseUrl}${url}`, data, jwt, params)
    }
    async _createOrUpdate(url, data, _jwt, params = {}) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        if (data.id) return fetch(`${url}/${data.id}?${qs.stringify(params, { encode: false })}`, {
            body: JSON.stringify({ data: data }),
            method: 'PUT',
            credentials: 'include',
            mode: 'cors',
            headers: {
                'content-type': 'application/json',
                ...token
            }
        })
            .then(res => res.json())
            .then(async res => {
                if (!res.error) {
                    return await purgeData(res?.data || res)
                }
                else throw res.error.message
            })

        else return fetch(`${url}`, {
            body: JSON.stringify({ data: data }),
            method: 'POST',
            credentials: 'include',
            mode: 'cors',
            headers: {
                'content-type': 'application/json',
                ...token
            }
        }).then(res => res.json())
            .then(async res => {
                if (!res.error) {
                    return await purgeData(res?.data || res)
                }
                else throw res.error.message
            })
        // return purgeData(response)
    }

    async post_Server(url, data, jwt) {
        'use server'
        return this._post(`${serverBaseUrl}${url}`, data, jwt)
    }
    async post(url, data, jwt) {
        return this._post(`${baseUrl}${url}`, data, jwt)
    }
    async _post(url, data, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        return fetch(`${url}`, {
            body: JSON.stringify(data),
            method: 'POST',
            credentials: 'include',
            mode: 'cors',
            headers: {
                'content-type': 'application/json',
                ...token
            }
        }).then(res => res.json())
            .then(async res => {
                if (!res.error) {
                    return await purgeData(res)
                }
                else throw res.error.message
            })
    }
    async delete_Server(url, data, jwt) {
        'use server'
        return this._delete(`${serverBaseUrl}${url}`, data, jwt)
    }
    async delete(url, data, jwt) {
        return this._delete(`${baseUrl}${url}`, data, jwt)
    }
    async _delete(url, data, _jwt) {
        if (_jwt || jwt) token = { "Authorization": "Bearer " + (_jwt || jwt) }
        return fetch(`${url}`, {
            body: JSON.stringify(data),
            method: 'DELETE',
            credentials: 'include',
            mode: 'cors',
            headers: {
                'content-type': 'application/json',
                ...token
            }
        }).then(res => res.json())
            .then(async res => {
                if (!res.error) {
                    return await purgeData(res)
                }
                else throw res.error.message
            })
    }
}

export async function purgeData(data) {
    var result = null
    if (data) {
        if (Array.isArray(data)) {
            result = data.map(x => purge(x))
        }
        else {
            result = purge(data)
        }
    }
    return result
}


function purge(data, key) {
    // return data || null
    if (data) {
        var result = null
        if (Array.isArray(data)) {
            result = []
            result = data.map(x => purge(x))
        }
        else
            if (typeof (data) === 'object') {
                result = {}
                Object.entries(data).map(([k, v]) => {
                    if (k === "attributes") {
                        if (key) {
                            result[key] = purge(data[k], key) || null
                        }
                        else result = { ...result, ...purge(data[k]) }
                    }
                    // else if (k === "data" && (typeof(data[k]) === "object" || Array.isArray(data[k]))) {
                    //     result = purge(data[k], key) || null
                    // }
                    else {
                        result[k] = purge(v, key) || null
                    }
                })
            }
            else {
                result = data || null
            }
        return result || null
    }
    return null
}

export async function purgeData2(data) {
    var result = null
    if (data) {
        if (Array.isArray(data)) {
            result = data.map(x => purge2(x))
        }
        else {
            result = purge2(data)
        }
    }
    return result
}

function purge2(data, key) {
    if (data) {
        var result = null
        if (Array.isArray(data)) {
            result = []
            result = data.map(x => purge2(x))
        }
        else if (typeof (data) === 'object') {
            result = {}
            Object.entries(data).map(([k, v]) => {
                if (k === "attributes") {
                    if (key) {
                        result[key] = purge2(data[k], key) || null
                    }
                    else result = { ...result, ...purge2(data[k]) }
                }
                else if (k === "data" && (typeof (data[k]) === "object" || Array.isArray(data[k]))) {
                    result = purge2(data[k], key) || null
                }
                else {
                    if (result) result[k] = purge2(v, key) || null
                }
            })
        }
        else {
            result = data || null
        }
        return result || null
    }
    return null
}


const API = Object.freeze(new APISingleton());
export default API;
