export async function getCourseById(
    id,
    params
) {
    let course = (await API.find(`/courses/${id.split('-')[0]}`, params)
        .then(cs => {
            return cs
        }))?.data

    return {
        ...course,
        path: `/courses/${id}`,
    };
}

export async function getLessonById(id,
    params, { jwt }
) {
    const lesson = (id + "").split("-")[1]
    const course = (id + "").split("-")[0]
    let resLesson = await API.findOne(`/lessons/${lesson}`, {

        ...params
    }, jwt)
        .then(cs => {
            return purgeData2(cs)
        })
    return {
        ...resLesson,
        path: `/courses/lessons/${course}/${lesson}`,
    };
}

export async function getModules({courseId, domain, jwt}){
    let result = await API.get(`/modules`, {
        domain: domain?.startsWith("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
        filters: {
            course: courseId,
        },
        populate: {
            lessons: {
                fields: ['slug', 'name']
            }
        }
    }, jwt)
        .then(cs => {
            return purgeData2(cs)
        })
    return result
}
