export async function getAllProducts_Server(
    domain,
    { skip = 0,
        limit }, jwt
) {
    return API.get_Server(`/users/me`, {
        fields: ["email"],
        populate: {
            configs: {
                fields: ["id", "logo", "name"],
                filters: {
                    domain: domain?.startsWith("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
                },
                populate: {
                    products: {
                        fields: ["id", "thumbnail", "name", "price"],
                        // populate:{
                        //     "payment_link": { 
                        //       fields: ["*"]
                        //     }
                        // }
                    }
                }
            }
        }
    }, jwt).then(cs => {
        return { products: cs?.configs[0]?.products || [], homepage: cs?.configs[0]?.homepage?.id || null }
    })
}


export const loadProductById_Server = async (id, domain, jwt) => {
    if (id && jwt) {
        const response = await API.get_Server(`/users/me`, {
            fields: ["email"],
            populate: {
                configs: {
                    fields: ["id", "logo", "name", "domain"],
                    filters: {
                        domain: domain?.startsWith("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
                    },
                    populate: {
                        products: {
                            filters: {
                                id: id,
                            },
                            fields: ["name", "description", "thumbnail", "pictures","video", "presentation"],
                            populate: {
                                "payment_link": {
                                    fields: ["*"]
                                },
                                "tags": {fields:["*"]}
                            }
                        }
                    }
                }
            }
        })
        return { config: { id: response.configs[0]?.id, domain: response.configs[0]?.domain }, product: response.configs[0]?.products[0] || null }
    }
    return {}
}

