export async function getPrevNextLesson(
    cours,
    lesson
) {
    const lsns = cours?.modules?.map(m => m.lessons.map(l => ({ name: l.name, lesson: l.id, module: m.id, course: cours.id, path: getLessonPath(cours, m.id, l) }))
    ).flat()

    const index = lsns.findIndex(c => c.lesson === Number(lesson.id))
    const prev = lsns[index - 1] || null;
    const next = lsns[index + 1] || null;
    return { prev, next };
}

