import { useEffect, useState } from "react"
import Loading from "../loading/Loading"
import PopupSingleton from "../hooks/Popups"
import Section from "../section"
import LoadingBar from "../loading/LoadingBar"

const PlaceHolder = (props) => {

    const [loading, setLoading] = useState(true)
    const [data, setData] = useState()
    const refresh = (_data) => {
        console.log("showPlaceholder", _data)
        // if (_data?.id != data?.id) {
            setLoading(true)
            setData(_data)
            setTimeout(() => {
                setLoading(false)
            }, 1);
        // }
    }
    useEffect(() => {
        PopupSingleton.setPlaceholder(props.id, { id: props.id, title: props.title, refresh: refresh })
        if (props.custom?.defaultComponent) {
            PopupSingleton.showPlaceHolder(props.id, props.custom?.defaultComponent)
        }
        setLoading(false)
    }, [])

    return (
        <Loading loading={loading}>
            {data ? <Section data={data.children[0]} path="0" /> :
                <div>
                    <LoadingBar loading={true} />
                </div>}
        </Loading>
    )
}

export default PlaceHolder