import React from "react";
import Base from '../Base';
import Component from "../component";
import { containerTypes } from "../data/types";

const style = {};
const Section = ({ data, bp,rawChildren, jsonData, handleDelete, previewId, handlePaste, handleDrop, path, parent, activeStyle,onItemClick, className="",activeItem, ...props }) => {
    const element = data
    const layout = data.children
    
    const renderColumn = (el) => {
        return (
        containerTypes.includes(el.type) ? <Section
            key={el.id}
            data={el}
            parent={data}
            bp={bp}
            previewId={previewId}
            setParentIsActive={props.setParentIsActive}
        /> :
            <Component
            key={el.id}
            data={el}
            parent={data}
            bp={bp}
            previewId={previewId}
            setParentIsActive={props.setParentIsActive}
            />
        );
    };
    return (
        <Base
            {...props}
            key={element.id}
            parent={parent}
            path={path}
            bp={bp}
            {...element}
            previewId={previewId}
            style={{ ...style }}
            className={className+ " base " + element.type + " "}
            element={element}
            onItemClick={onItemClick}
            activeItem={activeItem}>
            {rawChildren || layout?.map((column, index) => {
                return (renderColumn(column));
            })}
        </Base>
    );
};
export default Section;