import { Grid, Text, Col } from '../components/base';
import { styled } from '@mui/material/styles';
import { Box } from '@mui/material'
import VariablesSingleton from '../Variables';
import { useEffect, useState } from 'react';
import Loading from '../loading/Loading';

export default ({ ...props }) => {
    const [values, setValues] = useState([])
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        setTimeout(() => {
            setValues(VariablesSingleton.getParsed("course.requirements.values", []))
            setLoading(false)
        }, 10);
    }, [])

    return (
        <Body className="preview">
            <Loading loading={loading}>
                <Grid>
                    {
                        values?.map((goal, index) =>
                            <Col className="column_G0M" sm={12} md={6} key={index}>
                                <i className={"ic icon_T2l " + goal.icon} style={{ width: 40, height: 40 }}></i>
                                <Text className="col" style={{ height: 'fit-content' }}>{goal.text}</Text>
                            </Col>
                        )
                    }
                </Grid>
            </Loading>
        </Body>)
}


//const Body = styled.div`
const Body = styled(Box)`
.column_G0M {
height: fit-content;
flex-direction: row;
flex-wrap: nowrap;
padding-top: 0px;
padding-left: 0px;
padding-right: 0px;
padding-bottom: 0px;
display: flex;
align-items: center;
}

.text_Z8h {
height: fit-content;
flex-direction: row;
flex-wrap: nowrap;
padding-top: 0px;
}
`