import Section from "../section"

const MenuContainer = (props) => {
    return <div className="menuContainer" style={{height: "100%", position: "relative"}}>
        <Section data={props.header} path="0" />
        <div style={{height: "100%", overflow: "auto"}}>
        <Section data={props.body} path="0"/>
        </div>
    </div>
}

export default MenuContainer