export const loadPageById = async (pageId, domain, jwt) => {
    var response = await API.findOne(`/templates/preview`, {
        domain: domain?.startsWith("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain,
        guid: !pageId || pageId === "__home__" ? "/" : pageId.replace(/\/$/, ""),
        populate: {
            header: { fields: "*", populate: {leftsidebar: {fields: ["*"]}} },
            leftsidebar: { fields: "*" },
            rightsidebar: { fields: "*" },
            footer: { fields: "*" },
            config: { fields: ["id", "domain"] }
        }
    }, jwt)
    var { content: contentHeader, ...header } = response?.header || {}
    header = { ...header, tId: header?.id || null, ...contentHeader }
    var { content: contentLeftsidebar, ...leftsidebar } = header?.leftsidebar || response?.leftsidebar || {}
    leftsidebar = { ...leftsidebar, tId: leftsidebar?.id || null, ...contentLeftsidebar }
    var { content: contentRightsidebar, ...rightsidebar } = response?.rightsidebar || {}
    rightsidebar = { ...rightsidebar, tId: rightsidebar?.id || null, ...contentRightsidebar }
    var { content: contentFooter, ...footer } = response?.footer || {}
    footer = { ...footer, tId: footer?.id || null, ...contentFooter }

    if (response?.content) {
        const content = response.content
        const popups = {...response?.popups, ...content?.popups, ...footer?.popups, ...leftsidebar?.popups, ...header?.popups}
        return { config: response?.config, variables: response.used_variables, page: { ...content, variables: response.variables, tId: response.id, path: response.path, thumbnail: response.thumbnail, logo: response.logo, name: response.name, description: response.description, header: header, leftsidebar, rightsidebar, footer, popups } }
    }
    else {
        return { config: null, page: null }
    }
}

export async function parseResponse(response, { guid }) {
    var { content: contentHeader, ...header } = response?.header || {}
    header = { ...header, tId: header?.id || null, ...contentHeader }
    var { content: contentLeftsidebar, ...leftsidebar } = header?.leftsidebar || response?.leftsidebar || {}
    leftsidebar = { ...leftsidebar, tId: leftsidebar?.id || null, ...contentLeftsidebar }
    var { content: contentRightsidebar, ...rightsidebar } = response?.rightsidebar || {}
    rightsidebar = { ...rightsidebar, tId: rightsidebar?.id || null, ...contentRightsidebar }
    var { content: contentFooter, ...footer } = response?.footer || {}
    footer = { ...footer, tId: footer?.id || null, ...contentFooter }

    if (response?.content) {
        const content = response.content
        const popups = { ...response?.popups, ...content?.popups, ...footer?.popups, ...leftsidebar?.popups, ...header?.popups }

        return response.variables ? Variables.loadVariables(response.variables).then((variables) => {
            return { ...content, guid: guid, variables: variables || null, tId: response.id, path: response.path, thumbnail: response.thumbnail, logo: response.logo, name: response.name, description: response.description, header: header, leftsidebar, rightsidebar, footer, popups }
        }) : { ...content, variables: null, guid: guid, tId: response.id, path: response.path, thumbnail: response.thumbnail, logo: response.logo, name: response.name, description: response.description, header: header, leftsidebar, rightsidebar, footer, popups }
    }
    else {
        return null
    }
}

