export const loadPreview = async ({ id, domain, jwt, type, url, preview = false, loadPage }) => {

    var _config = null, resource = null, _page = null
    var used_variables = {}
    if (type != "oio-admin") {

        if (id != 'undefined') {
            switch (type) {
                case "page":
                    var { config, page } = preview ? await loadEditById_Server(id, domain, type, jwt, true) : await loadPage(id, domain)
                    _config = config || null
                    _page = page
                    break;
                case "course":
                    var { template, prev, next, ..._resource } = await getCoursePresentation(id, domain, jwt)
                    resource = _resource
                    // console.log("loadingCourse4", domain, type, id, template?.header?.popups)
                    _page = template?.content ? { ...template?.content, header: template?.header, footer: template?.footer, leftsidebar: template?.leftsidebar, } : null
                    if (resource?.id) used_variables = {
                        ...template?.used_variables, ...flattenJSON(resource, "course", true),
                        "lesson.next": next || null, "lesson.prev": prev || null
                    }
                    if (_page) _page.popups = { ...template?.popups, ...template?.header?.popups, ...template?.footer?.popups, ...template?.leftsidebar?.popups, }

                    delete resource?.template
                    // console.log(used_variables)
                    _page = parsedBody(_page, used_variables, { course: resource })
                    break;
                case "lesson":
                    const lessonId = id

                    if (lessonId && lessonId != 'undefined') {
                        const { lesson, course, template, next, prev } = (await getFullLesson({ lessonId }, domain, jwt)) || {}

                        _page = template?.content || null

                        used_variables = {
                            ...template?.used_variables, ...flattenJSON(course, "course", true), ...flattenJSON(lesson, "lesson", true),
                            "lesson.next": next || null, "lesson.prev": prev || null
                        }

                        _page = parsedBody(_page, used_variables, { course, lesson })

                    }
                    break;
                case "product":
                    let { template, ..._resource } = await getProductPresentation(id, domain, jwt)
                    resource = _resource
                    // console.log("loadingCourse4", domain, type, id, template?.header?.popups)
                    _page = template?.content ? { ...template?.content, header: template?.header, footer: template?.footer, leftsidebar: template?.leftsidebar, } : null
                    if (resource?.id) used_variables = {
                        ...template?.used_variables, ...flattenJSON(resource, "product", true)
                    }
                    if (_page) _page.popups = { ...template?.popups, ...template?.header?.popups, ...template?.footer?.popups, ...template?.leftsidebar?.popups, }

                    delete resource?.template
                    // console.log(used_variables)
                    _page = parsedBody(_page, used_variables, { course: resource })
                    break;
                case "payment-link":
                    resource = await getPaymentFull(id, domain, jwt)
                    _page = resource?.template?.content || null
                    if (resource?.id) used_variables = { ...resource?.template?.used_variables, id: id }

                    delete resource?.template
                    if (resource) Object.entries(resource).map(([k, v]) => used_variables["payment." + k] = v)

                    _page = parsedBody(_page, used_variables, { payment_link: resource })
                    console.log("loadingPaymentLink", domain, type, id)
                    break;
                default:
                    var { config, page } = await loadEditById_Server(id, domain, type, jwt)
                    used_variables = resource?.template?.used_variables
                    _config = config || null
                    _page = page
                    _page = parsedBody(page, used_variables, {})
                    break;
            }
            if (_page && resource) {
                _page.name = resource.name
                _page.description = resource.description || null
            }
        }
        console.log("loadPreview", domain, type, id, type, _page?.id, _page?.type)
        return {
            config: _config || null,
            page: _page || null,
            resource: resource || null,
            type: type || null,
            variables: { ...used_variables, "global.url": url || null, "global.jwt": jwt || null, "global.domain": domain?.includes("localhost") ? process.env.NEXT_DEFAULT_DOMAIN : domain, "global.type": type },
            jwt,
            url
        }
    }
    return {}
}


