import BasketProducts from "../pay/BasketProducts"

const Panier = ({ children, bp,show, ...props }) => {
    const sideBarWidth = show ? 0 : (props[bp]?.style?.width ||props.md?.style?.width || props.style?.width || 300)
    const overflow = show ? 'visible' : 'hidden'
    return <div onClick={e => e.stopPropagation()} style={{transition: 'all 0.3s ease', marginRight: -sideBarWidth,overflow, position: "absolute", width: 300,  right: 0, height: "100%"}}>
        <BasketProducts light={true} />
        {children}</div>
}


export default Panier