import React, { useState,} from 'react';

export default function ({ placeholder, type, label, name, value, updContent, field, options, values, ...props }) {
	const [content, setContent] = useState(value || props.defaultValue)
	const onChange = (e) => {
		setContent(e.target.value);
	}

	return <div className='w-100'>
		<label>{label || field.label || name || field.name}</label>
		<select
			value={content || ""}
			label={label || field.label || name || field.name}
			onChange={onChange}
		>
			{(options || field.options)?.map(option => <option key={option.label} value={option.value}>{option.label}</option>)}
		</select>
	</div>
};