

let instance;
let watchers ={}
class ScreenSizeWatcherSingleton {
  constructor() {
    if (instance) {
      throw new Error("You can only create one instance!");
    }
    instance = this;
  }

  getInstance() {
    return this;
  }
  addWatcher(id, watch, minWitdh, maxWidth){
    watchers[id] = {
        id,
        watch,
        minWitdh,
        maxWidth
    }
  }

  watch(screenWidth){
    Object.values(watchers).forEach(el => el.watch(screenWidth))
  }

}

const ScreenSizeWatcher = Object.freeze(new ScreenSizeWatcherSingleton());
export default ScreenSizeWatcher;
