import * as React from 'react';
import Component from '.';
import Section from '../section';
import { styled } from '@mui/material/styles';
import { Box } from '@mui/material'

export default function Folder({ content, ...props }) {
    return (<>
        <Body style={{ position: "relative" }}>
            <details open={props.custom?.opened != 'false'}>
                <summary>
                    <Section  {...props.header} data={props.header} path="0" />
                </summary>
            </details>
            <div className="content-details">
                {props.body?.type === "folder" ? <>
                    <Component {...props.body} data={props.body} path="0" />
                </> :
                    <Section {...props.body} data={props.body} path="0" />
                }
            </div>
        </Body>
    </>
    )
}


//const Body = styled.div`
const Body = styled(Box)`
/***********************************************/
/********* Details *****************************/
/***********************************************/
details {
  overflow: hidden; /* Keep this line to prevent an odd blue outline around the element in Safari. */
 }
 summary {
  display: block; /* This hides the summary's ::marker pseudo-element */
 }
 summary::-webkit-details-marker {
  display: none; /* This also hides the ::marker pseudo-element, but in Safari */
 }
 div.content-details { 
  box-sizing: border-box;
  max-height: 0;
  overflow: hidden;
  transition: max-height 200ms ease-out;
 }
 details[open] + div.content-details {
  max-height: 800px; /* Set a max-height value enough to show all the content */ 
  transition: max-height 400ms ease-out, border 0ms linear;
 }
 details[open] i {
  rotate: 90deg;
  transition: rotate 200ms ease-out;
 }
`