import React, { useEffect, useState } from 'react'
import Section from '../section';
import styled from 'styled-components';

const TableEditor = ({ inputs = {}, name, databases = [], ...props }) => {  

    // console.log("minprops", props)
    return (
        <div style={{width: "100%", overflowX: "auto", overflowY: "hidden", paddingBottom: 10}}>
        <table style={{ overflowX: "auto", maxWidth: "100%", width: "100%", position: "relative" }}>
            {props.items?.map((row, index) => <tr key={index}>
                {row?.map((col, index2) => <td key={col.id} style={{ height: 10, minWidth: 30,
                        maxWidth: props.custom?.cellMaxWidth + 'px', 
                        width: props.custom?.cellMinWidth+ 'px!important',  position: "relative" }}>
                    <Section data={{ type: "custom", style: { height: "100%", display: 'flex', padding: "0px!important" }, id: index2, children: [col] }} path={index2 + ""} />
                </td>)}</tr>)}
        </table>
        </div>
    )
}

export default TableEditor