import { useEffect, useState } from "react"
import { styled } from '@mui/material/styles';
import { Box } from '@mui/material'
import Section from "../section";

const NavList = ({ ...props }) => {
    const [activeIndex, setActiveIndex] = useState(-1)
    const [activeLevel0, setActiveLevel0] = useState(0)
    const [activeLevel1, setActiveLevel1] = useState(0)
    const bp = props.bp

    const handleClick = (index, id) => {
        console.log("props.body?.onClick?.type", props.body)
        setActiveIndex(index)

        if (props.onClickIndex) {
            props.onClickIndex(index, id)
        } else {
            const onClick = props.body && props.body["onClick_" + bp] || props.body?.onClick
            const mapping = onClick?.mapping
            if (mapping && mapping[index]) {
                if (onClick?.type === "placeHolder") PopupSingleton.showPlaceHolder(onClick?.id, mapping[index])
                else if (onClick?.type === "page") PopupSingleton.showPage(mapping[index])
                else PopupSingleton.showPopup(mapping[index])

                VariablesSingleton.setPreview(props.xdata, index)
            }
        }
    }

    const RenderCategory = ({ index, category, children }) => <>
        <details key={category.name} className="folder_JNZ" open={activeLevel0 === index} onClick={(e) => { e.stopPropagation(); setActiveLevel0(index) }}>
            <summary>
                <Section data={{ ...props.header, type: "grid", forwardClick: true  }} rawChildren={<>
                    {category.icon && <Section path="0" data={{ ...props.header?.children[0], xtype: "component", type: 'c', forwardClick: true }} rawChildren={<i className={"ic icon_8tI_3 " + category.icon} style={{ height: "100%", minWidth: 20, minHeight: 20 }}></i>} />}
                    <Section path="1" data={{ ...props.header?.children[1], xtype: "component", type: 'c', forwardClick: true }} rawChildren={<span>{category.name}</span>} />
                    {props.xdata.category_right_icon && <Section path="2" data={{ ...props.header?.children[2], xtype: "component", type: 'c', forwardClick: true }} rawChildren={<i className={"ic icon_8tI_3 toogle " + props.xdata.category_right_icon} style={{ height: "100%", minWidth: 20, minHeight: 20 }}></i>} />}
                </>} />
            </summary>
        </details>
        <div className="content-details">
            {
                category.sections?.map((sct, indexSct) => <><details open={activeLevel1 === indexSct}>
                    <summary onClick={(e) =>  { e.stopPropagation(); setActiveLevel1(indexSct)}}>
                        <Section data={{ ...props.body, type: "grid", forwardClick: true  }} rawChildren={<>
                            {sct.icon && <Section path="0" data={{ ...props.body?.children[0], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={sct.icon && <i className={"ic icon_8tI_3 " + sct.icon} style={{ height: "100%", minWidth: 20, minHeight: 20 }} ></i>} />}
                            <Section path="1" data={{ ...props.body?.children[1], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={<span>{sct.name}</span>} />
                            {props?.xdata?.section_right_icon && <Section path="2" data={{ ...props.body?.children[2], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={<i className={"ic icon_8tI_3 toogle " + props.xdata?.section_right_icon} style={{ height: "100%", minWidth: 20, minHeight: 20 }}></i>} />}
                        </>} />
                    </summary>
                </details>
                    <div className="content-details">{
                        sct?.pages?.map((pg, indexPg) => <Section data={{ ...props.footer, type: "grid" }} rawChildren={<>
                            {pg.icon && <Section path="0" data={{ ...props.body?.children[0], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={pg.icon && <i className={"ic icon_8tI_3 " + pg.icon}></i>} />}
                            <Section path="1" data={{ ...props.body?.children[1], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={<span>{pg.name}</span>} />
                            {props.xdata?.page_right_icon && <Section path="2" data={{ ...props.body?.children[2], xtype: "component", type: 'c', forwardClick: true  }} rawChildren={<i className={"ic icon_8tI_3 toogle " + props.xdata?.page_right_icon} style={{ height: "100%", minWidth: 20, minHeight: 20 }}></i>} />}

                        </>} />
                        )
                    }
                    </div>
                </>)
            }
        </div>
    </>

    return (

        <Body className="preview">
            {props.xdata?.categories?.map((xd, index) => <RenderCategory category={xd} index={index} />
                // :
                //         <details key={xd.name} className="folder_JNZ" open={true}>
                //             <summary>
                //                 <Section data={{ ...props.header, type: "grid" }} rawChildren={<>
                //                     <Section data={{ ...props.header?.children[0], type: "container" }} rawChildren={<i className={"ic icon_8tI_3 " + xd.icon} style={{ height: '40px', width: '40px' }}></i>} />
                //                     <Section data={{ ...props.header?.children[1], type: "container" }} rawChildren={<span>{xd.name}</span>} />
                //                     <Section data={props.header?.children[2]} />
                //                 </>} />
                //             </summary>
                //             {xd.sections.map(sct => <div className="jIp">
                //                 {
                //                     <details open={true}>
                //                         <summary>
                //                             <Link href={sct.path || '#'} className="section_123">
                //                                 {sct.icon ? <i className={"ic icon_8tI " + sct.icon} style={{ height: '40px', width: '20px', marginRight: "0.75rem" }}></i> : <div style={{ width: 10 }} />}
                //                                 {sct.name}
                //                             </Link>
                //                         </summary>
                //                     </details>
                //                 }
                //                 {
                //                     <div className="content-details" style={{ marginBottom: 15 }}>
                //                         {sct.pages?.map(pg => <div style={{ display: "flex" }}>
                //                             {pg.icon ? <i className="ic icon_8tI fa fa-chevron-right" style={{ height: '40px', width: '40px' }}></i> : <div style={{ width: 30 }} />}
                //                             <Text className="col text_T8f">{pg.name}</Text>
                //                         </div>)}
                //                     </div>
                //                 }
                //             </div>)}
                //         </details>
            )}
        </Body>)
}

export default NavList



//const Body = styled.div`
