import * as React from 'react';
import Section from '../section';
import VariablesSingleton from '../Variables';
import Link from 'next/link';
import { getPreviousLesson } from '../utils/course';
import LoadingCircular from '../loading/LoadingCircular';
export default function PrevLesson({ content, bp, ...props }) {

    const [loading, setLoading] = React.useState(true)
    const { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } = (props.md?.style || {})
    const { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } = (props.sm?.style || {})
    const [previous, setPrevious] = React.useState(props?.custom?.lesson_prev?.path)
    const [lesson, setLesson] = React.useState()

    React.useEffect(() => {
        if (VariablesSingleton.getParsed("lesson.slug")) getPreviousLesson({
            courseId: VariablesSingleton.getParsed("cours.slug"),
            lessonId: VariablesSingleton.getParsed("lesson.slug")
        }, VariablesSingleton.getParsed("global.domain"), VariablesSingleton.getParsed("global.jwt")).then(res => {
            console.log(res)
            if (res) {
                setPrevious(res.previous_path)
                setLesson(res.lesson_slug)
            }
            else {
                document.getElementById(props.id).style.visibility = "hidden"
            }
            setLoading(false)
        })

    }, [VariablesSingleton.getParsed("lesson.slug")])




    return (
       !previous ? <div /> : <LoadingCircular loading={loading}>
            <a href={previous} onClick={() => { VariablesSingleton.set("lesson.slug", lesson); setLoading(true) }}>
            <Section
                parent={props}
                disabled={true}
                isNew={true}
                bp={bp}
                data={{
                    id: "child_" + props.id, children: props.children,
                    md: { style: { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } },
                    sm: { style: { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } },

                }}
                path="0" />
        </a>
        </LoadingCircular>
    )
}