import { getPixelValue } from "./baseUtils"

export function getJsonKey(dict, path) {
    if (dict) {
        const keys = path.split('.')
        var currentPath = dict[keys[0]]
        // console.log(currentPath)
        keys.shift()
        if (keys.length > 0) {
            currentPath = getJsonKey(currentPath, keys.join('.'))
        }
        return currentPath
    }
    return undefined
}

export function setJsonKey(dict, path, value) {
    const keys = path.split('.')
    console.log("setJsonKey", dict, path, value)
    if (keys.length === 1) {
        dict[keys[0]] = value
        return dict
    }
    else {
        const keys = path.split('.')
        const currKey = keys.shift()
        var currValue = getJsonKey(dict, currKey)
        if (currValue) {
            if (Array.isArray(currValue)) {
                const index = keys.shift()
                if (index < currValue.length) currValue[index] = setJsonKey(currValue[index], keys.join("."), value)
                else currValue[index] = value
            }
            else currValue = setJsonKey(currValue, keys.join("."), value)
        }
        else {
            currValue = {}
            currValue = setJsonKey(currValue, keys.join("."), value)
        }
        dict[currKey] = currValue
    }
    return { ...dict }
}

export function cssToJson(cssString) {
    if (cssString) {
        //split string into css rules
        var JSONarray = cssString.split("}");

        //for each rule
        for (var i = 0; i < JSONarray.length; i++) {
            //split selector and properties
            JSONarray[i] = JSONarray[i].split("{");
            //for each property
            for (var j in JSONarray[i]) {
                //split key:value pairs
                JSONarray[i][j] = JSONarray[i][j].split(";");
                //for each key value pair
                for (var k in JSONarray[i][j]) {
                    //split key and value
                    JSONarray[i][j][k] = JSONarray[i][j][k].split(":");
                }
            }
        }

        //rebuild string with JSON syntax
        var jsonStr = "";
        //for each rule
        for (var i = 0; i < JSONarray.length; i++) {
            jsonStr += '"' + JSONarray[i][0] + '" {'
            for (var j = 0; j < JSONarray[i].length; j++) {
                for (var k = 0; k < JSONarray[i][j].length; k++) {
                    jsonStr += '"' + JSONarray[i][j][k][0] + '":"' + JSONarray[i][j][k][1] + '" '
                }
            }
            jsonStr += '}'
        }
        console.log(jsonStr)
        //alert(jsonStr);
        return jsonStr;
    }
}

