export const parseCookie = str => {
    const output = {}
    str
        .split(',').map(c =>
            c.split(";")[0].split('='))
        .reduce((acc, v) => {
            var tmp = decodeURIComponent(v[1].trim())
            if (tmp.startsWith("{")) output[decodeURIComponent(v[0].trim())] = JSON.parse(tmp)
            else output[decodeURIComponent(v[0].trim())] = tmp
        }, {})
    return output
}

export const parseReqCookie = str => {
    const output = {}
    str
        .split(';').map(c =>
            c.split('='))
        .reduce((acc, v) => {
            var tmp = decodeURIComponent(v[1].trim())
            if (tmp.startsWith("{")) output[decodeURIComponent(v[0].trim())] = JSON.parse(tmp)
            else output[decodeURIComponent(v[0].trim())] = tmp
        }, {})
    return output
}


export function JSToCSS(JS, important = "") {
    let cssString = "";
    for (let objectKey in JS) {
        const current = JS[objectKey]
        if (objectKey != "w" && objectKey != "h" && objectKey != "fixedH" && objectKey != "fixedW" && !current?.toString()?.includes('${')) {
            // if(objectKey === "marginRight" && current?.startsWith('-')){
            //     objectKey = "paddingRight"
            // }
            // else if(objectKey === "marginLeft" && current?.startsWith('-')){
            //     objectKey = "paddingLeft"
            // }
            if ((objectKey === "justifyContent" && JS[objectKey] === "center") || (objectKey === "justifyContent" && JS[objectKey] === "center")) {
                cssString += "display: flex;"
            }
            if (JS[objectKey] && JS[objectKey] != '' && JS[objectKey] != 'null' && JS[objectKey] != 'undefined') cssString += objectKey.replace(/([A-Z])/g, (g) => `-${g[0].toLowerCase()}`) + ": " + getPixelValue(JS, objectKey) + important + ";";
        }
    }
    return cssString;
}