

export function useLoaded({
    crossOrigin,
    referrerPolicy,
    src
  }, cb) {
      if (!src) {
        return undefined;
      }
      cb(false)
      let active = true;
      const image = new Image();
      image.onload = () => {
        if (!active) {
          return;
        }
        cb('loaded')
      };
      image.onerror = () => {
        if (!active) {
          return;
        }
        cb('error')
      };
      image.crossOrigin = crossOrigin;
      image.referrerPolicy = referrerPolicy;
      image.src = src;
  }