import React, { useEffect, useRef, useState } from 'react';
import { Fade, Slide, Zoom } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import styled from 'styled-components';
import Section from '../section'
import ActiveSingleton from '../Active';


const CarouserContainer = styled.div`
`
const CarouselButton = styled.button`
position: absolute;

cursor: pointer;

top: 50%;
z-index: 150;

transition: transform 0.1s ease-in-out;

background: white;
border-radius: 15px;
border: none;
padding: 0.2rem;
`
const LeftCarouselButton = styled(CarouselButton)`
left: 0;
transform: translate(-100%, -50%);

${CarouserContainer}:hover & {
  transform: translate(10%, -50%);
}
`

const RightCarouselButton = styled(CarouselButton)`
right: 0;
transform: translate(100%, -50%);

${CarouserContainer}:hover & {
  transform: translate(-10%, -50%);
}
`

const properties = () => ({
  prevArrow: <LeftCarouselButton id={"left-btn"}>
      <i className='fa fa-chevron-left' style={{ width: 20 }}></i>
    </LeftCarouselButton>,
  nextArrow: <RightCarouselButton id={"right-btn"}>
       <i className='fa fa-chevron-right' style={{ width: 20}}></i>
  </RightCarouselButton>
})

const FadeExample = ({ updProps, ...props }) => {
  const [activeIndex, setActiveIndex] = useState(0)
  const [slides, setSlides] = useState(props.children || [])
  const [autoSlide, setAutoSlide] = useState(props.custom?.autoSlide || 5)
  const transitionDuration = autoSlide * 100
  const [paused, setPaused] = useState(false)
  const bp = ActiveSingleton.getBp()
  const slideRef = useRef(null)

  useEffect(() => {
    if (parseInt(autoSlide) > 0 && !paused) {
      setTimeout(() => {
        setActiveIndex(activeIndex < slides?.length - 1 ? activeIndex + 1 : 0)
      }, parseInt(autoSlide) * 1000);
    }
  }, [activeIndex, paused])
  const height = props[bp]?.style?.height || props.md?.style?.height

  const typeRender = (c) => {
    switch (props.custom?.type) {
      case "multiple":
        return <Slide slidesToScroll={Math.min(props.custom?.slidesToScroll, slides.length)} slidesToShow={Math.min(props.custom?.slidesToShow, slides.length)} indicators={true} autoplay={autoSlide > 0} transitionDuration={transitionDuration} {...properties()}>
          {c}
        </Slide>
      default:
        switch (props.custom?.transitionType) {
          case "fade":
            return <Fade ref={slideRef} autoplay={autoSlide > 0} transitionDuration={transitionDuration} {...properties()}>
              {c}
            </Fade>
          case "dezoom":
            return <Slide ref={slideRef} autoplay={autoSlide > 0} transitionDuration={transitionDuration} {...properties()}>
              {c}
            </Slide>
          case "zoom":
            return <Zoom ref={slideRef} autoplay={autoSlide > 0} transitionDuration={transitionDuration} {...properties()}>
              {c}
            </Zoom>
          default:
            return <Slide ref={slideRef} autoplay={autoSlide > 0} transitionDuration={transitionDuration} {...properties()}>
              {c}
            </Slide>
        }
    }
  }
  return (
    <CarouserContainer style={{ position: "absolute", height: "100%", overflow: "hidden" }}>
        {
          typeRender(
            [].concat(slides).map((el, index) => <div
              style={{
                width: '100%', width: "100%", height: height,
               }}
              className="each-slid" key={el.id}>
              <Section data={{ ...el, [bp]: { ...el[bp], fixedH: true, style: { height: "100%", width: "100%", ...el[bp]?.style } }, style: { ...el.style, overflow: "hidden" } }} activeIndex={activeIndex} path="0" />
            </div>
            )
          )}
    </CarouserContainer>
  );
};

export default FadeExample;