import * as React from 'react';
import { parseContent } from '../utils/stringUtils';
import ReactPlayer from 'react-player/lazy';
import Section from '../section';

export default function BackgroundVideo({ content, bp, ...props }) {
  const [value, setValue] = React.useState()
  const [loading, setLoading] = React.useState(true)

  const updateValue = (_val) => {
    setValue(_val)
  }
  React.useEffect(() => {
    console.log("useeffect", content)
    parseContent(content, updateValue)
  }, [content]);
  const width = props[bp]?.style?.width || props.md?.style?.width
  const height = props[bp]?.style?.height || props.md?.style?.height || 400

  const background = {
    backgroundColor: 'yellow',
    backgroundImage: "url(" + props?.custom?.preview + ")",
    backgroundSize: '100%',
    backgroundPosition: "center",
    backgroundRepeat: 'no-repeat'
  }
  return (
    <div style={{width, height}}>
      {!value ? <div style={{ width: "100%", height: "100%", position: "absolute", display: 'flex', alignItems: 'center', justifyContent: 'center', ...background }}>
        <i class="fa fa-play" style={{ color: 'whitesmoke', backgroundColor: 'black', border: "1px solid gray", padding: "12px 15px", borderRadius: 100, width: 40, height: 40 }} aria-hidden="true"></i>
      </div> : <ReactPlayer url={value} height={height} onReady={() => { setLoading(false) }} />
      }
      <div  style={{ width: "100%", height: "100%", position: "absolute", backgroundColor: "red", opacity: .3}} ></div>
      <div style={{zIndex: 100}}>
        <Section 
              disabled={true}
            parent={props}
          data={{
            id: "children_"+props.id, 
            children: props.children
            }} />
      </div>
    </div>
  )
}