import * as React from 'react';
import { enrollCourse, hasAccess } from '../utils/course';
import VariablesSingleton from '../Variables';
import { useRouter } from "next/navigation";
import PaiementModal from '../PaiementModal';
import { useSession } from 'next-auth/react';
import { addToPaymentLink, updateBasket } from '../utils/payment';
import PopupSingleton from '../hooks/Popups';

export default function Enroll({ content, ...props }) {
    const [access, setAccess] = React.useState(false)

    const {data: session} =useSession()
    React.useEffect(() => {
        if(session?.jwt) {checkAccess()}
    }, [session])
    const router = useRouter()
    const onClick = () => {
        console.log(VariablesSingleton.getParsedVariables())
        const jwt = session?.jwt
        if (jwt) {
            if(access){
                enrollCourse(VariablesSingleton.getParsed("course.guid"), VariablesSingleton.getParsed("global.domain"), VariablesSingleton.getParsed("global.jwt")).then(res => {
                    console.log("res", res)
                    // if (res?.next_path) window.location = `${res?.next_path}`
                    // if (res?.next_path) window.location = "https://" + VariablesSingleton.getParsed("global.domain") + res?.next_path
                    if (res?.next_path) window.location = res?.next_path
                }).catch(e => {
                    console.error(e)
                    if (e === "Forbidden") {
                        router.push(`https://plateforme.me/auth/signin?redirect_url=${VariablesSingleton.getParsed("global.url")}`)
                    }
                })
            }
            else{
                // create or get payment link
                updateBasket({
                    jwt,
                    resource_guid: VariablesSingleton.getParsed("course.guid"),
                    resource_type: "course",
                    domain: VariablesSingleton.getParsed("global.domain")
                })
                .then(res => {
                    // console.log(res)
                    // router.push(`${process.env.NEXT_PUBLIC_PAYMENT_URL}/${res?.guid}`)
                    // router.push(`${process.env.NEXT_PUBLIC_URL_PREFIX}/panier`)
                    PopupSingleton.tooglePanier(true)
                })
            }
        } else {
            router.push(`https://plateforme.me/auth/signin?redirect_url=${VariablesSingleton.getParsed("global.url")}`)
        }
    }

    const checkAccess = async () => {
        const jwt = session?.jwt
        console.log("chekAccess", jwt)
        if (VariablesSingleton.getParsed("course.guid", null)) return hasAccess(VariablesSingleton.getParsed("course.guid"), VariablesSingleton.getParsed("global.domain"), jwt).then(res => {
            console.log("getProgression", res)
            if(res?.has_access) {
                setAccess(true)
                // window.location = "https://" + VariablesSingleton.getParsed("global.domain") + res?.next_path
                return true
            }
            return false
        })
        return false
    }

    return (
        <>
        {access ? <div style={{width: "100%", height: "100%"}} onClick={onClick}>Continuer</div>:
         <div style={{width: "100%", height: "100%"}} onClick={onClick}>{content}</div>
    }
        </>
    )
}