import * as React from 'react';
import { parseContent } from '../utils/stringUtils';
import ReactPlayer from 'react-player/lazy';
import LoadingCircular from '../loading/LoadingCircular';

export default function Video({ content, bp, ...props }) {
    const [value, setValue] = React.useState()
    const [loading, setLoading] = React.useState(true)
    const [height, setHeight] = React.useState(props[bp]?.style?.height || props.md?.style?.height)
    const [width, setWidth] = React.useState(props[bp]?.style?.width || props.md?.style?.width)
  
    const updateValue = (_val) => {
      setValue(_val)
    }
      React.useEffect(() => {
        setLoading(true);
        setTimeout(() => {
          parseContent(content, updateValue)
          setHeight(props[bp]?.style?.height || props.md?.style?.height)
          setWidth(props[bp]?.style?.width || props.md?.style?.width)
          setLoading(false)
        }, 200);
      }, [content,  props[bp]?.style?.heigh]);

      console.log("video", height)
      const background = {
        backgroundColor: 'black',
        backgroundImage: "url("+props?.custom?.preview+")",
        backgroundSize: '100%', backgroundPosition: "center", backgroundRepeat: 'no-repeat'
      }
    return <LoadingCircular loading={loading}>{!value ? <div style={{width, height: height || 200, display: 'flex', alignItems: 'center',justifyContent: 'center', ...background }}>
        <i className="fa fa-play" style={{color: 'whitesmoke', backgroundColor: 'black', border: "1px solid gray", padding: "12px 15px", borderRadius: 100, width: 40, height: 40}} aria-hidden="true"></i></div> 
        : 
        !value?.startsWith("http") ? <iframe title={"Iframe"}  allowFullScreen height={"100%"} width={"100%"} style={{ objectFit: "cover" }} src={`https://embed.api.video/vod/${value}`} /> :
          <ReactPlayer url={value} height={parseInt(height) > 0 ? parseInt(height) : height} width={"100%"} onReady={()=> {setLoading(false)}}/>}
          </LoadingCircular>
}