import { useState, useEffect } from 'react';
import { Grid, Text, Row, Col, Image, Button } from '../components/base';
import { styled } from '@mui/material/styles';
import Link from 'next/link';
import { Box, Input } from '@mui/material'
import API from '../utils/API';
import { useSession } from 'next-auth/react';
import Loading from '../loading/Loading';
import { parseDomain } from '../utils/stringUtils';

const uniqueResources = [
    "course",
]
const devises = {
    "Euro": "€",
    "Dollar": "$"
}
export default ({ logo, guid: plink_guid, light = false, devise, updResources, updPlink }) => {

    const [totalQuantity, setTotalQuantity] = useState(0)
    const [totalPrice, setTotalPrice] = useState(0)
    const [totalPriceFCFA, setTotalPriceFCFA] = useState(0)
    const [_resources, setResources] = useState({})
    const [loading, setLoading] = useState(true)
    const { data: session } = useSession()
    const [domain, setDomain] = useState(session?.domain)

    useEffect(() => {
        setLoading(true)
        setDomain(parseDomain(session?.domain))
        if (session?.jwt) API.get(`/payment-links/session`, {
            domain: parseDomain(session?.domain)
        }, session?.jwt)
            .then(res => {
                console.log("pay", res)
                if(updPlink) updPlink(res.guid)
                var total = 0
                var totalFCFA = 0
                var quantity = 0

                Object.values(res.resources).map(resource => {
                    total += resource.price * resource.quantity
                    totalFCFA += resource.price_fcfa * resource.quantity
                    quantity += resource.quantity
                })
                setTotalPrice(total)
                setTotalPriceFCFA(totalFCFA)
                setTotalQuantity(quantity)
                setResources(res.resources)
                setLoading(false)
            })
            // .catch(e => setLoading(false))

    }, [session?.jwt])

    const updQuantity = (guid, quantity, increase = true, type) => {
        console.log("updQuantity")
        const _res = { ..._resources }
        const resource = _res[guid]
        if (quantity === 0) {
            resource.quantity = 0
            delete _res[guid]
        }
        else {
            if (quantity) resource.quantity = parseInt(quantity)
            else {
                if (increase) {
                    resource.quantity += 1
                    setTotalQuantity(totalQuantity + 1)
                }
                else {
                    if (resource.quantity > 0) {
                        resource.quantity -= 1
                        setTotalQuantity(totalQuantity - 1)
                    }
                }
            }
            if (resource?.quantity > 0) _res[guid] = { ...resource }
            else delete _res[guid]
        }
        var total = 0
        var totalFCFA = 0
        var quantity = 0
        Object.values(_res).map(resource => {
            total += resource.price * parseInt(resource.quantity)
            totalFCFA += resource.price_fcfa * parseInt(resource.quantity)
            quantity += parseInt(resource.quantity)
        })
        setTotalPrice(total)
        setTotalPriceFCFA(totalFCFA)
        setTotalQuantity(quantity)
        setResources(_res)
        if (updResources) updResources(_res)

        API.post(`/payment-links/update_basket`, { guid: plink_guid, resource_guid: guid, quantity: resource.quantity, resource_type: type, domain: domain }, session?.jwt)
    }

    return (

        <Body className="preview">
            {light && <Grid className="grid_3ub_2">
                <div className="col" style={{ height: 'fit-content', fontFamily: 'verdana, geneva, sans-serif', fontSize: '16px' }}><strong style={{ fontSize: '14px' }}>{'Votre panier'}</strong></div>
            </Grid>}
            <Row className="col-md section_eNh" sm={12}>
                {!light && <Grid className="grid_3ub">
                    <Link className="image_sWb zoom-in-hover-normal" style={{ height: 'fit-content', maxWidth: '70px' }} href={`https://${domain}`}>
                        <img src={logo || "https://storage.googleapis.com/public.outil.io/medias/1/images/logo.webp"} alt='concepteur.io' /></Link>

                    <Link className="col" href={`https://${domain}`} style={{ height: 'fit-content', marginLeft: '10px', textAlign: 'right', fontFamily: 'verdana, geneva, sans-serif', fontSize: '16px' }}><strong style={{ fontSize: '14px' }}>{'Votre panier'}</strong></Link>
                </Grid>}
                <Loading loading={loading}>
                    <Text style={{ height: 'fit-content', paddingBottom: '5px', fontSize: '18px' }} md={12}>Total ({totalQuantity} articles)<strong>: {devises[devise] || "€"}{totalPrice}{totalPriceFCFA ? " | " + totalPriceFCFA + " FCFA" : ""}</strong></Text>

                    {Object.values(_resources).map((resource, index) => <Grid className="grid_5cr" md={12} key={index}>
                        <Col className="column_Sxl">
                            <Link className="image_7VF zoom-in-hover-normal" style={{ height: 'fit-content', marginBottom: '10px' }} href={`/${resource.type}/${resource.guid}`}>
                                <img src={resource.thumbnail} alt='concepteur.io' style={{ borderRadius: "10px" }} /></Link>
                            <Col className="column_A6a">
                                <i className="ic icon_wro fa fa-minus" disabled={uniqueResources.includes(resource.type)} onClick={() => !uniqueResources.includes(resource.type) && updQuantity(resource.guid, null, false, resource.type)}></i>
                                <Input className="col" disabled={uniqueResources.includes(resource.type)} style={{ height: 'fit-content', textAlign: 'center', padding: "0px 5px", with: "100%", height: 20, fontSize: 12 }} value={resource.quantity} onChange={(e) => !uniqueResources.includes(resource.type) && updQuantity(resource.guid, e.target.value, null, resource.type)} />
                                <i className="ic icon_wro fa fa-plus" disabled={uniqueResources.includes(resource.type)} onClick={() => !uniqueResources.includes(resource.type) && updQuantity(resource.guid, null, true, resource.type)}></i>
                            </Col>
                        </Col>
                        <Col className="col column_h14"><Text style={{ height: 'fit-content', paddingBottom: '5px', textAlign: 'left', fontFamily: 'verdana, geneva, sans-serif', fontSize: '14px' }} md={12}>{resource.name}</Text>
                            <Text style={{ height: 'fit-content', paddingBottom: '5px', fontSize: '18px' }} md={12}><strong>{devises[devise] || "€"}{resource.price}{resource.price_fcfa ? " | " + resource.price_fcfa + " FCFA" : ""}</strong></Text>
                            <Button className="bt button_8PB" style={{ textAlign: 'center' }} onClick={() => updQuantity(resource.guid, 0, true, resource.type)}>Supprimer</Button></Col>
                    </Grid>)}
                </Loading>
                {!light && <Col className="column_kvx">
                    <Link className="image_S8u zoom-in-hover-normal" href='https://outil.io'>
                        <Text style={{ height: 'fit-content', marginTop: 2, marginRight: 5, textAlign: 'left', fontFamily: 'verdana, geneva, sans-serif', fontSize: '10px', width: "auto" }}>Fourni par</Text>
                        <img src="https://storage.googleapis.com/public.outil.io/medias/1/images/logo.webp" width="50px" alt='concepteur.io' />
                    </Link>
                </Col>}
            </Row>
            {light && <Grid className="grid_3ub_3">
                <Link href={`${process.env.NEXT_PUBLIC_URL_PREFIX}/panier`} className="col" style={{ height: 'fit-content', fontFamily: 'verdana, geneva, sans-serif', fontSize: '16px' }}><strong style={{ fontSize: '14px' }}>Commander</strong></Link>
            </Grid>}
        </Body>
    )
}


