import React, { useEffect, useState,} from 'react';

export default function ({ placeholder, type, label, name,id, value, updContent, field, options, values, ...props }) {
	const [content, setContent] = useState(value || props.defaultValue)
	const [height, setHeight] = useState(props[props.bp]?.style?.height || "100%")
	const onChange = (e) => {
		setContent(e.target.value);
	}
	const multiline = type === 'text-area' || type === 'textarea' || type == "message"
	return <div className='w-100'>
		{/* {label && <label>{label || field.label || name || field.name}</label>} */}
		{
			multiline ? <textarea type={type} id={id} value={content} onChange={onChange} name={name || id} placeholder={placeholder}></textarea>
			: <input className='w-100' type={type} id={id} required={field.required} value={content} onChange={onChange} name={name || id} placeholder={placeholder} />
		}
		
	</div>
};