import React, { useEffect, useState } from "react"
import PopupSingleton from "./Popups"
import Section from "../section"

const PopupView = ({ children, bp, show, sideBarWidth, ...props }) => {
    const ref = React.useRef(null)
    const [element, setData] = useState({})
    const [open, setOpen] = useState(false)
    useEffect(() => {
        PopupSingleton.initBuilder(ref)
    }, [])

    React.useImperativeHandle(ref, () => ({
        setOpen,
        // showPopup,
        setData
    }))
    
    return <div ref={ref} onClick={() => setOpen(false)}>
        {open && <div className="popup-builder preview">
            <div className='popup-section' onClick={(e) => e.stopPropagation()}>
                {
                    <Section data={element} bp={bp} previewData={element.previewData} path="0" parent={element}/>
                }
            </div>
        </div>}
    </div>
}

export default PopupView