import * as React from 'react';
import { parseContent } from '../utils/stringUtils';

export default function StarRating({ content, ...props }) {
  const [value, setValue] = React.useState()

  const updateValue = (_val) => {
    setValue(_val)
  }
  
  React.useEffect(() => {
    parseContent(content, updateValue)
  }, [content]);

  return (

    <div className="star-rating">
      {[5,4,3,2,1].map(s => <span key={s}>
        <input type="radio" id={s+"-stars"} name="rating" value={s} />
        <label htmlFor={s+"-stars"} className="star">&#9733;</label>
      </span>)}
    </div>
  )
}