import API from "./utils/API";

let instance;
let counter = 0;
let session =null
let variables = {}
let parsedVariables = {}
let scoped = {}
let usedVariables = {}
let screenWidth
let screenHeight

const loading = {}
class Variables {
  constructor() {
    if (instance) {
      throw new Error("You can only create one instance!");
    }
    instance = this;
  }

  getInstance() {
    return this;
  }

  getAll() {
    return variables;
  }
  getVariablesName() {
    return Object.entries(variables).map(([k, v]) => ({ label: k, value: v.guid, name: v.name, type: v.type  }));
  }
  async getVariablesFromBackend(_session){
    variables = {}
    return API.get(`/variables/all`, {domain: _session?.domain}, _session?.jwt).then(res => {
      res?.map(v => variables[v.name] = v)
      return variables
    })
  }
  getParsedVariables() {
    return parsedVariables;
  }

  getUsedVariables(){
    return usedVariables
  }

  increment() {
    return ++counter;
  }

  decrement() {
    return --counter;
  }

  init(_variables) {
    // console.log(_variables)
    if (_variables) variables = { ...variables, _variables }
  }
  setSession(_session){
    session = _session
  }
  set(name, value) {
    parsedVariables[name?.replace("${", "")?.replace("}")] = value
  }
  setRaw(name, value) {
    variables[name?.replace("${", "")?.replace("}")] = value
  }

  setParsedVariables(_parsedVariables) {
    parsedVariables = _parsedVariables
  }
  addParsed(path, _variable) {
    parsedVariables = {...parsedVariables, ...this.flattenJSON(path?.replace("${", "")?.replace('}', ""), _variable)}
  }

  getChildVariable(name, index = -1, defaultValue = null) {
    const keys = name?.split('.')
    const _parent = parsedVariables[keys[0]]

    if (keys.length > 1) {
      console.warn('getChildVariable', index, name, _parent, parsedVariables[name])
      var values
      if(Array.isArray(_parent)){
        values = _parent?.map(r => r[keys[1]])
      } else {values = _parent}
      parsedVariables[name] = values
      usedVariables[name] = true
      delete loading[keys[0]]
      return index > -1 ? values[index] || defaultValue : values || defaultValue
    }
    else {
      delete loading[keys[0]]
      return _parent
    }
  }

  getParsed(name, defaultValue){
    return parsedVariables[name] || defaultValue ||name
  }
  async get(name, reloadCb, defaultValue = null, index) {
    if (name && typeof (name) === "string") {
      name = name?.replace("${", "").replace("}", "")
      if (parsedVariables[name]) {
        usedVariables[name] = true
        return reloadCb(parsedVariables[name])
      }
      const keys = name?.split('.')
      const _parent = keys[0]
      // console.log("check loading " + name, _parent, loading[_parent], parsedVariables[_parent], variables[_parent])

      if (parsedVariables[_parent]) {
        if (variables[_parent]?.type === "JSON") {
          return reloadCb(this.getChildVariable(name, 0))
        }
        return reloadCb(this.getChildVariable(name, index))
      }
      else if (!loading[_parent]) {
        loading[_parent] = true
        return reloadCb(await this.loadVariable(name, reloadCb) || defaultValue)
      }
      // else if (loading[_parent]) {
      //   setTimeout(async () => {
      //     return await this.get(name, reloadCb, defaultValue)
      //   }, 500)
      //   // sleep(10000)
      //   // return await this.get(name, defaultValue)
      // }
      // while(loading[name] && parsedVariables[_parent]){

      // }
      // else {
      //   setTimeout(()=> {
      //     return this.get(name, defaultValue)
      //   }, 10000)
      // }
    }
    return defaultValue
  }

  getSync(name, reloadCb, defaultValue = null, index) {
    if (name && typeof (name) === "string") {
      name = name?.replace("${", "").replace("}", "")
      if (parsedVariables[name]) return parsedVariables[name]
      const keys = name?.split('.')
      const _parent = keys[0]
      // console.log("check loading " + name, _parent, loading[_parent], parsedVariables[_parent], variables[_parent])

      if (parsedVariables[_parent]) {
        if (variables[_parent]?.type === "JSON") {
          return this.getChildVariable(name, 0)
        }
        return this.getChildVariable(name, index)
      }
      else if (!loading[_parent]) {
        loading[_parent] = true
        return this.loadVariableSync(name, reloadCb) || defaultValue
      }
    }
    return defaultValue
  }


  async getRaw(name, reloadCb, defaultValue = null, index) {
    if (name) {
      name = name.replace("${", "").replace("}", "")
      if (parsedVariables[name]) return reloadCb(parsedVariables[name])
      const keys = name?.split('.')
      const _parent = keys[0]
      // console.log("check loading " + name, _parent, loading[_parent], parsedVariables[_parent], variables[_parent])

      if (parsedVariables[_parent]) {
        if (variables[_parent]?.type === "JSON") {
          return reloadCb(this.getChildVariable(name, 0))
        }
        return reloadCb(this.getChildVariable(name, index))
      }
      else if (!loading[_parent]) {
        loading[_parent] = true
        return reloadCb(await this.loadVariable(name, reloadCb) || defaultValue)
      }
      // else if (loading[_parent]) {
      //   setTimeout(async () => {
      //     return await this.get(name, reloadCb, defaultValue)
      //   }, 500)
      //   // sleep(10000)
      //   // return await this.get(name, defaultValue)
      // }
      // while(loading[name] && parsedVariables[_parent]){

      // }
      // else {
      //   setTimeout(()=> {
      //     return this.get(name, defaultValue)
      //   }, 10000)
      // }
    }
    return defaultValue
  }

  getColumns(name) {
    return variables[name].cols || []
  }

  async setScoped(elementId, key, value) {
    // console.log("scoped variables", elementId, key, value)
    scoped[elementId] = { [key]: value }
  }
  async getScoped(elementId, key) {
    return scoped[elementId] ? scoped[elementId][key] : null
  }

  flattenJSON = (path = "", json, arrayLevel = -1) => {
    var result = {}
    if (json && Array.isArray(json)) {
      arrayLevel += 1
      json.map((obj1, index) => {
        result = Object.assign({}, result, this.flattenJSON(path + "[" + index + "]", obj1, arrayLevel))
      })
    }
    else if (json && typeof (json) === "object") {
      Object.entries(json).map(([k, v]) => {
        result = Object.assign({}, result, this.flattenJSON(path + "." + k, v, arrayLevel))
      })
    }
    result[path] = json
    return result
  }
  async loadVariable(name) {
    if (name) {
      usedVariables[name] = true

      name = name?.replace("${", "").replace("}", "")
      const _var = variables[name.split(".")[0]]
      var canExec = true
      if (_var) {
        if (_var.type === 'SQL') {
          for (var i = 0; i < _var.query.split(";").length; i++) {
            const query = _var.query.split(";")[i]
            if (query.toUpperCase().startsWith("DROP")
              || query.toUpperCase().startsWith("DELETE")
              || query.toUpperCase().startsWith("TRUNCATE")
              || query.toUpperCase().startsWith("UPDATE")
              || query.toUpperCase().startsWith("INSERT")) {
              canExec = false;
              break;
            }
          }
          if (!canExec) return
          var result = await API.rawSQL('/sqls/query', {
            sql: _var.query,
            dashboard: this.tId,
            variables: _var.variables,
            config: _var.dbConfig,
            meta: _var
          })
          result = result || {}
          parsedVariables[_var.name] = result.data
          delete loading[_var.name]
          if (result?.type === 'map') {
            const row = []
            result.cols.map(col => {
              parsedVariables[_var.name + '.' + col] = result.data[col]
              delete loading[_var.name + '.' + col]
              if (result.count > 0) {
                row.push((result.data[col] || [])[0])
              }
            })
            variables[_var.name]["cols"] = result.cols
            variables[_var.name]['rows'] = row
          } else {
            variables[_var.name]["cols"] = []
            variables[_var.name]['rows'] = [result.data]
          }
          this.setParsedVariables(parsedVariables)
          return parsedVariables[name]
        }
        else if (_var.type === 'RESOURCES') {
          console.log("variableConfig", variables["global.domain"], _var)
          return _var
        }
        else {
          let tmpJSONVar = _var.query
          try { tmpJSONVar = JSON.parse(tmpJSONVar) }
          catch (e) { }
          parsedVariables[name.split(".")[0]] = tmpJSONVar

          if (tmpJSONVar) parsedVariables = Object.assign({}, parsedVariables, this.flattenJSON(name.split(".")[0], tmpJSONVar))
          console.warn("loadVariable",variables,parsedVariables, name, tmpJSONVar)
          this.setParsedVariables(parsedVariables)
          return parsedVariables[name]
        }
      }
      else {
        delete loading[name.split(".")[0]]
        return name
      }
    }
  }

  loadVariableSync(name) {
    name = name?.replace("${", "").replace("}", "")
    const _var = variables[name.split(".")[0]]
    if (_var) {
      let tmpJSONVar = _var.query
      try { tmpJSONVar = JSON.parse(tmpJSONVar) }
      catch (e) { }
      parsedVariables[name] = tmpJSONVar

      if (tmpJSONVar) parsedVariables = Object.assign({}, parsedVariables, this.flattenJSON(name, tmpJSONVar[0]))
      // if(name.includes("[") && name.includes("]")){
      //   const index = parseInt(name.split(".")[1]?.replace("[", "").replace("]",""))
      //   parsedVariables =  Object.assign({}, parsedVariables, this.flattenJSON(name+".", tmpJSONVar[index]))
      // }

      this.setParsedVariables(parsedVariables)
      return parsedVariables[name]
    }
    else {
      delete loading[name.split(".")[0]]
      return name
    }
  }

  setPreview(name, index) {
    name = name.replace('${', "").replace("}", "")
    const parsed = parsedVariables[name]
    if (parsed && parsed[index]) {
      Object.entries(this.flattenJSON(name, parsed[index])).map(([k, v]) => {
        parsedVariables[k] = v
      })
      this.setParsedVariables(parsedVariables)
    }
  }

  async loadVariables(_vars, cb) {
    variables = { ...variables, ..._vars }
    return variables
  }

  // extractVariables (element, vars = {}) {
  //   element.map(el => )
  // }
}

const getOut = () => {
  return VariablesSingleton.getParsedVariables()
}

const VariablesSingleton = Object.freeze(new Variables());
export default VariablesSingleton;
