// "use client"
import React, { useState, useEffect } from "react";
import RowPreview from "./section";
import Loading from "./loading/Loading";
import Active from './Active'
import LeftMenu from "./hooks/LeftMenu";
import PopupSingleton from "./hooks/Popups";
import RightMenu from "./hooks/RightMenu";
import VariablesSingleton from "./Variables";
import PopupView from "./hooks/PopupView";
import 'animate.css';
import OnScreenSingleton from "./hooks/OnScreen";
import API from "./utils/API";
import ScreenSizeWatcher from "./hooks/ScreenSizeWatcher";
import Panier from "./hooks/Panier";

const Preview = ({ page, domain, jwt, type = "page", _bp, variables = {}, externalMethods = {} }) => {
  const [loading, setLoading] = useState(false);
  const [loadingPage, setLoadingPage] = useState(false);
  const [showLeftMenu, setShowLeftMenu] = useState(false)
  const [showRightMenu, setShowRightMenu] = useState(false)
  const [showPanier, setShowPanier] = useState(false)
  const [bp, setBp] = useState(_bp || "sm")
  const bottomNavbar = page?.footer;
  const header = page?.header;
  const leftSideBar = page?.leftsidebar;
  const rightSideBar = page?.rightsidebar;
  const [isDev, setIsDev] = useState(false)
  const [previewId, setPreviewId] = useState()

  const renderRow = (row, currentPath, parent) => {
    return (
      <RowPreview
        key={row.id}
        data={row}
        parent={parent}
        bp={bp}
        previewId={previewId}
      />
    );
  };

  const toogleLeftMenu = (value) => {
    const _data = PopupSingleton.showLeftMenu(value)
    setTimeout(() => {
      setShowLeftMenu(_data)
    }, 1);
  }
  const toogleRightMenu = () => {
    const _data = !PopupSingleton.showRightMenu()
    setTimeout(() => {
      setShowRightMenu(_data)
    }, 1);
  }
  const tooglePanier = (value) => {
    const _data = PopupSingleton.showPanier(value)
    setTimeout(() => {
      setShowPanier(_data)
    }, 1);
  }
  Active.setBp(_bp, setBp)

  useEffect(() => {
    const preId = Date.now().toString(36) + Math.random().toString(36).substring(2)
    setPreviewId(preId)
    Active.loadMethods(preId, externalMethods)
    VariablesSingleton.setSession({domain: domain, jwt: jwt})
    setLoading(true)
    setLoadingPage(true)
    if (!domain.includes("localhost")) API.setBaseUrl("https://" + domain + "/api")
    // if (Active.initBp) Active.initBp(setBp)

    OnScreenSingleton.init()
    console.log("preveiw variables", variables, page.variables)
    VariablesSingleton.setParsedVariables(variables)

    // console.log(window.innerWidth, page)
    // const _bp_ = window.innerWidth <= 900 ? "sm" : "md"
    PopupSingleton.loadPopups(page.popups || {})
    PopupSingleton.setPlaceholder("leftMenu", { id: "leftMenu", toogleLeftMenu })
    PopupSingleton.setPlaceholder("rightMenu", { id: "rightMenu", toogleRightMenu })
    PopupSingleton.setPlaceholder("panier", { id: "panier", tooglePanier })
    toogleLeftMenu(_bp === "md" ? true : false)
    VariablesSingleton.loadVariables(page?.variables).then(res => {setLoadingPage(false); setLoading(false)})

  }, [])

  const handleResize = () => {
    ScreenSizeWatcher.watch(window.innerWidth, window.innerHeight)
    setBp(window.innerWidth <= 700 ? "sm" : "md")
  };
  useEffect(() => {
    window.addEventListener('resize', handleResize);
    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (
    <div>
      {isDev && <div className="left-right" style={{ padding: "0px 10px", position: "sticky", top: 0, left: 0, width: "100%", height: 30, zIndex: 4000, backgroundColor: "black" }} >
        <a href={process.env.NEXT_PUBLIC_URL_PREFIX + `/templates/${type}/edit/${page?.tId}`} style={{ fontSize: 13, color: "white", backgroundColor: "gray", padding: "5px 10px", borderRadius: 5 }}>Modifier</a>
        <i className="fa fa-close" style={{ backgroundColor: "red", color: "white", height: 20, width: 20, marginTop: 5, textAlign: 'center' }} onClick={() => setIsDev(false)}></i>
      </div>}
      <div className="builder-main-layout preview" style={{
        position: "relative",
        width: "100%",
        maxWidth: "100%",
        alignSelf: "center",
        // height: "100vh",
        // overflowY: "auto"
      }}
        onClick={() => Active.activate({})}>
        {header && header.id && <div className="builder-main-header">
          <Loading loading={loading}>
            {renderRow(header, "0", header)}
          </Loading>
        </div>}
        <div className="builder-main-content">
          {leftSideBar && leftSideBar.id && <div className="builder-main-sideBar" onClick={(e) => { e.stopPropagation(); if (bp === "sm") toogleLeftMenu() }} style={{ backgroundColor: bp === "sm" ? "#7975753b" : "7975753b", width: bp === "sm" && showLeftMenu ? "100%" : "fit-content", zIndex: bp === "sm" ? 1100 : 'auto', top: 0, height: bp === "sm" && showLeftMenu ? "calc(100vh)" : '', position: bp === "sm" ? 'absolute' : 'relative' }}>
            <LeftMenu bp={bp} show={showLeftMenu} {...leftSideBar}>
              {renderRow(leftSideBar, "0", leftSideBar)}
            </LeftMenu>
          </div>}
          <div className="builder-main-body">
            <Loading loading={loadingPage}>
              {renderRow(page, "0", page)}
            </Loading>
          </div>
        </div>
        {bottomNavbar && bottomNavbar.id && <div className="builder-main-bottomNavbar">
          <Loading loading={loading}>
            {renderRow(bottomNavbar, "0", bottomNavbar)}
          </Loading>
        </div>}
      </div>
      {rightSideBar && rightSideBar.id && <div className="builder-main-sideBar" onClick={(e) => { if (bp === "sm") toogleRightMenu() }} style={{ backgroundColor: showRightMenu ? "#7975753b" : "transparent", width: "100%", zIndex: !showRightMenu ? -100 : 2000, top: 0, height: "100vh", position: 'fixed' }}>
        <div style={{ width: "fit-content" }} onClick={(e) => e.stopPropagation()}>
          <RightMenu bp={bp} show={showRightMenu}>
            {renderRow(rightSideBar, "0", rightSideBar)}
          </RightMenu>
        </div>
      </div>}
      {showPanier && <div className="builder-main-sideBar panier" onClick={(e) => { tooglePanier() }} style={{ backgroundColor: showRightMenu ? "#7975753b" : "transparent", width: "100%", top: 0, height: "100vh", position: 'fixed' }}>
        <div style={{ width: "fit-content" }} onClick={(e) => e.stopPropagation()}>
          <Panier bp={bp} show={showPanier}>
          </Panier>
        </div>
      </div>
      }
      <PopupView bp={bp} />
    </div>
  );
};
export default Preview;