import { useEffect, useState } from "react";
import Active from '../Active'
import { usePathname } from 'next/navigation'
import Section from "../section";
import PopupSingleton from "../hooks/Popups";
import API from "../utils/API";
import { useSession } from "next-auth/react";

const varRegex = /\$\{[aA-zZ0-9 -_]*\}/g
const parseContent = (content, values) => {
    var result = content
    if (typeof result == 'string') {
        const vars = result?.match(varRegex);
        for (var i = 0; i < vars?.length; i++) {
            const c = vars[i]
            const newVal = values[c.replace("${", "").replace("}", "")] || ''
            if (newVal && typeof newVal != 'array' && typeof newVal != 'object') {
                result = result.replace(c, newVal)
            } else result = newVal
        }
    }
    return result
}


const Form = (props) => {
    const [bp, setBp] = useState(props.bp)
    const [popup, setPopup] = useState(false)
    const path = usePathname()
    const {data: session} = useSession()

    const handleSubmit = async (e) => {
        // e.preventDefault()
        e.stopPropagation()
        // console.log("handlesubmit", e)
        const result = {}
        for (var i = 0; i < e.target.elements.length; i++) {
            const el = e.target.elements[i]
            if (el.name) {
                result[el.name] = el.value?.replace(/"/g, '\\"')
            }
            if (el.type === 'email') {
                result["email"] = el.value?.replace(/"/g, '\\"')
            }
            if (el.type === 'name') {
                result["name"] = el.value?.replace(/"/g, '\\"')
            }
            if (el.type === 'phone') {
                result["phone"] = el.value?.replace(/"/g, '\\"')
            }
        }

        e.preventDefault()

        var response
        switch (props.custom?.submitType) {
            case "sql":
                // console.log(props.custom.execInsert)
                break;
            case "email":
                response = await API.post(`${process.env.NEXT_PUBLIC_DIRECT_EMAIL_API}/${props.id}`, {
                    to: props.custom?.email,
                    path: path,
                    domain: session?.domain,
                    subject: props.custom?.subject,
                    name: props.content,
                    tags: ['tag: ' + props.custom?.tag],
                    from: {
                        name: result?.name,
                        email: result?.email,
                        phone: result?.phone
                    },
                    ...result,
                })
                    .catch(e => {
                        console.log(e)
                        if(props.custom?.errorMessage) alert(props.custom?.errorMessage || "Une erreur s'est produite. \nVeuillez nous excuser pour la gène occasionnée")
                    })
                break;
            case "workflow":
                response = await API.post(`${process.env.NEXT_PUBLIC_DIRECT_WORKFLOW_API}/${props.custom?.event}`, {
                    to: { email: props.custom?.email },
                    path: path,
                    domain: API.getConfig()?.domain,
                    subject: props.custom?.subject,
                    name: props.content,
                    tags: ['tag: ' + props.custom?.tag],
                    ...result,
                })
                    .catch(e => {
                        console.log(e)
                        if(props.custom?.errorMessage) alert(props.custom?.errorMessage || "Une erreur s'est produite. \nVeuillez nous excuser pour la gène occasionnée")
                    })
                break;
            case "url":
                console.log(props.custom)
                if (props.custom?.url) {
                    console.log(props.custom.url)
                    response = await fetch(props.custom?.url, {
                        method: props.custom?.method || "POST",
                        body: JSON.stringify(result),
                    }).then(res => res.json())
                        .catch(e => {
                            console.log(e)
                            if(props.custom?.errorMessage) alert(props.custom?.errorMessage || "Une erreur s'est produite. \nVeuillez nous excuser pour la gène occasionnée")
                        })
                }
                break;
            default:
                console.log(props.custom)
                break;
        }
        if(!response ||  response.status >= 400) {
            if(!props.custom?.errorMessage) alert("Une erreur s'est produite. \nVeuillez nous excuser pour la gène occasionnée")
        }
        else{
            console.log('form res', response)
            switch (props.custom?.onSuccessType) {
                case "url":
                    window.location = props.custom?.onSuccess
                    break;
                case "popup":
                    PopupSingleton.showPopupPreview(props.custom?.onSuccess);
                    break;
                default:
                    break;
            }
        }
    }

    const { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } = (props.md?.style || {})
    const { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } = (props.sm?.style || {})
    return <>
        <form onSubmit={(e) => handleSubmit(e)}>
            <Section
                parent={props}
                bp={bp}
                data={{
                    id: "child_" + props.id, children: props.children,
                    md: { style: { flexDirection: mdFlexDirection, flexWrap: mdFlexWrap, display: mdDisplay, alignItems: mdAlignItems } },
                    sm: { style: { flexDirection: smFlexDirection, flexWrap: smFlexWrap, display: smDisplay, alignItems: smAlignItems } }
                }}
                path="0" />
        </form>
        {popup?.children && open && <div className="popup-builder popup-modal" onClick={handleClose} style={{ ...popup.style, zIndex: auto, position: "fixed", display: open ? "flex" : "none" }}>
            <div className='popup-section modal-content'>
                {
                    popup?.children?.map((c, index) => <div key={index}><Section data={c} bp={bp}/></div>)
                }
            </div>

        </div>}
    </>
}

export default Form