import slugify from "slugify";
import VariablesSingleton from "../Variables";
import Variables from "../Variables"
import { getJsonKey } from "./jsonParser";

export function size({sm,md,xs}){
  var w =''
  if(xs) w = 'col-'+xs
  else{
      w = sm ? ' col-sm-'+sm:''
      w+= md ? ' col-md-'+md:' '
  }
  return w
}
export function guid(len) {
  var buf = [],
    chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789',
    charlen = chars.length,
    length = len || 4;

  for (var i = 0; i < length; i++) {
    buf[i] = chars.charAt(Math.floor(Math.random() * charlen));
  }

  return buf.join('');
}

export const toSlug = (text) => {
  if (!text) text = ''
  return slugify(text.toLowerCase()).replace(/,/g, "_").replaceAll(/\'/g, "_").replaceAll(/\-/g, "_").replaceAll(/ /g, "_");
}

export function getString(value, defaultValue) {
  if (value != undefined && value != null && value != '') {
    return value
  }
  if (defaultValue != undefined) return defaultValue
  return ''
}


export function getObject(obj, path, defaultValue = '') {
  var value = defaultValue
  if (obj) {
    const keys = path.split('.')
    var tmpObj = obj
    keys.map(key => {
      if (typeof (tmpObj) === 'object') {
        tmpObj = tmpObj[key]
      }
      value = tmpObj ? tmpObj : defaultValue
    })
    return value
  }
}


const varRegex = /\$\{[aA-zZ0-9 -_]*\}/g
export const parseContent = async (content, cb, index) => {
  var result = content
  if (typeof result == 'string') {

    const vars = result?.match(varRegex);
    // console.log("parseContent", content, vars)
    if (!vars) {
      return cb(result)
    }
    else {
      for (var i = 0; i < vars?.length; i++) {
        const c = vars[i]
        await Variables.get(c.replace("${", "").replace("}", ""), (newVal) => {
          if (newVal && typeof newVal != 'array' && typeof newVal != 'object') {
            result = result.replace(c, newVal)
          } else result = newVal
          cb(result)
        }, content, index)
      }
    }
    // cb(result)
  }
  return result
}

export const jsonParse = (content, values, prefix = "") => {
  var result = content
  if (typeof result === 'string') {

    const vars = result?.match(varRegex);
    if (!vars) {
      return JSON.parse(result)
    }
    else {
      for (var i = 0; i < vars?.length; i++) {
        const c = vars[i]
        // console.log("parseLocalContent2", c, values, values[c.replace("${", "").replace("}", "").replace(prefix + ".", "")])
        const newVal = values[c.replace("${", "").replace("}", "").replace(prefix + ".", "")]
        result = result.replace(c, newVal+""?.replaceAll('"', ''))
      }
    }
  }
  try{
    return JSON.parse(result) || {}
  }
  catch(e){
    console.error(e, result)
    return result
  }
}



export const parseLocalContent = async (dataKey, jsonObj, cb) => {
  var newBodies = []
  try {
    if (typeof (dataKey) === "string") {
      return VariablesSingleton.loadVariable(dataKey.replace("${", "").replace("}", ""))
        .then(data => {
          if (data) {

            if (Array.isArray(data)) {
              data?.map(d => {
                const newContent = jsonParse(
                  JSON.stringify(jsonObj), d, dataKey?.replace("${", "").replace("}", "")
                )
                newBodies.push(newContent)
              })
            }
            else {
              newBodies.push(data)
            }
          }
          cb({parsed: newBodies, data:data})
          return {parsed: newBodies, data:data}
        })
    }
    else if (typeof (dataKey) === "object") {
      const prefix = dataKey.prefix
      const data = dataKey.data
      const jsonString= JSON.stringify(jsonObj)//.replaceAll('"', '¤')
      data?.map(d => {
        const newContent = jsonParse(
          jsonString, d, prefix?.replace("${", "").replace("}", "")
        )
        newBodies.push(newContent)
      })
    }
    cb({parsed: newBodies, data:dataKey?.data})
    console.log("newBodies", newBodies)
    return {parsed: newBodies, data:dataKey?.data}
  }
  catch (e) {
    console.error(e);
    cb({parsed: newBodies, data: dataKey})
  }
}


export function getLessonPath(course, moduleId, lesson) {
  var path = '/'
  if (course?.id) {
    path = `/cours/lessons/${course.id}-${toSlug(course.name)}`
    if (lesson?.id) path = path + `/${moduleId}-${lesson.id}-${toSlug(lesson.name)}`
  }
  return path
}


export function getFirstLesson(cours) {
  const modules = cours?.modules
  if (modules?.length > 0) {
    return modules?.map(m => m.lessons?.map(l => ({ name: l.name, lesson: l.id, module: m.id, cours: course.id, path: getLessonPath(cours, m.id, l) }))
    ).flat()[0]
  }
  return {}
}
export function getCoursePath(course) {
  var path = '/'
  if (course?.id) {
    path = `/cours/${course.id}-${toSlug(course.name)}`
  }
  return path
}


export function getLocation(href) {
  var match = href.match(/^(https?\:)\/\/(([^:\/?#]*)(?:\:([0-9]+))?)([\/]{0,1}[^?#]*)(\?[^#]*|)(#.*|)$/);
  return match && {
    href: href,
    protocol: match[1],
    host: match[2],
    hostname: match[3],
    port: match[4],
    pathname: match[5],
    search: match[6],
    hash: match[7]
  }
}


export function parsedBody(body, used_variables, variables) {
  var bodyString = JSON.stringify(body) //{"test": "${course.instructeur.picture}"})
  Object.entries(used_variables || {}).map(([k, v]) => {
    const regex = new RegExp("${" + k + "}", 'g');
    const value = getJsonKey(variables, k) || v
    if (typeof (value) === "object" || Array.isArray(value)) {
      bodyString = bodyString?.replaceAll("\"${" + k + "}\"", JSON.stringify(value))
    }
    else {
      bodyString = bodyString?.replaceAll("${" + k + "}", JSON.stringify(value)?.replace('"', '')?.replace(/\"$/, ''))
    }
    // console.log("replace", k, v)
  })
  // console.log(used_variables, bodyString)
  return JSON.parse(bodyString)
}

export function parseDomain(domain) {
  return domain?.includes('localhost') ? process.env.NEXT_DEFAULT_DOMAIN || 'ebc.plateforme.me' : domain
}
export function getApiUrl(domain) {
  return domain?.includes('localhost') ? process.env.NEXT_DEFAULT_DOMAIN || 'https://ebc.plateforme.me/api' : 'https://' + domain + '/api'
}