import React from 'react';
import styles from './Loading.module.scss'

export default function ({ loading, message, children }) {
        return <>
                {loading ? <div className='p-4'>
                        {message && <h6>{message}</h6>}
                        <div className={styles.skeleton}>
                                <div className={styles.skeleton_left}>
                                        <div className={[styles.line,styles.h17,styles.w40, styles.m10].join(" ")}></div>
                                        <div className={styles.line}></div>
                                        <div  className={[styles.line,styles.h8, styles.w50].join(" ")}></div>
                                        <div className={[styles.line,styles.w75].join(" ")}></div>
                                </div>
                                <div className={styles.skeleton_right}>
                                        <div  className={styles.square}></div>
                                </div>

                        </div>
                </div> : <React.Fragment>{children}</React.Fragment>}
        </>
};